import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import Select from 'react-select';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from "../LanguageMenu";
import {
    URI_ADMIN_ENROLL,
    URI_ADMIN_STORES,
} from "../../constants/URIs";
import {useLocation, useNavigate} from "react-router-dom";
import {URL_MENU} from "../../constants/URLs";
import {ERR_POSTING_ENROLLMENT, ERR_VERIFYING_USER} from "../../constants/messages/ErrorMessages";

/**
 * Handles the page of enrollment of managers and admins by admin.
 *
 * @returns {Element}
 * @constructor
 */
const Enrollment = () => {
    const { t } = useTranslation('translations/admin/Enrollment');
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [email, setEmail] = useState('');

    const [stores, setStores] = useState([]);
    const [selectedRole, setSelectedRole] = useState({});

    const fetchStores= () => {
        fetch(URI_ADMIN_STORES, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
        })
            .then(response => {
                return response.json()
            })
            .then(data => {
                setStores(data)
            })
    }
    useEffect(() => {
        fetchStores();
    }, [])

    const roleOptions = [...stores.map((store) => (
        { value: 'Store: ' + store.name, label: 'Manager: ' + store.name }
    )), { value: 'Administrator', label: 'Admin' }];

    const postEnrollment = () => {

        let payload = {
            contact_data: email,
            user_status: selectedRole
        };

        // POST enrollment request
        fetch(URI_ADMIN_ENROLL, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response) {
                    alert(t('enrollment_successful'));
                    navigateToNextPage();
                }
                else {
                    return response.json().then((errorText) => {
                        throw new Error(errorText.detail);
                    });
                }
            })
            .catch(error => {
                alert(ERR_POSTING_ENROLLMENT + ' ' + error.message);
            })
    }

    const navigateToNextPage = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    const handleEmailInput = (value) => {
        setEmail(value);
    }

    const handleRoleChange = (selectedRoleOption) => {
        setSelectedRole(selectedRoleOption.value)
    }

    const handleEnrollClick = () => {
        postEnrollment();
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>

                <div className={'view-container'}>

                    <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                    <h1>{t('heading')}</h1>

                    <div className={'content-container'}>

                        <h3>{t('email')}</h3>
                        <input
                            className={'text-form'}
                            type='text'
                            onInput={e => handleEmailInput(e.target.value)}
                        />

                        <h3>{t('select_role')}:</h3>
                        <Select
                            closeMenuOnSelect={true}
                            placeholder={'None'}
                            options={roleOptions}
                            name='roles'
                            onChange={handleRoleChange}
                            className='basic-single'
                            classNamePrefix='select'
                        />

                        <div className={'button-list'}>
                            <Button className={'accent-button'} onClick={handleEnrollClick}>{t('enroll')}</Button>
                        </div>
                    </div>
                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Enrollment;