
import React, {startTransition, Suspense, useEffect, useState} from "react";
import 'react-calendar/dist/Calendar.css';
import '../../Style.css';
import {Button} from "antd";
import logo_img from "../../logo.png";
import {useTranslation} from "react-i18next";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import Loading from "../Loading";
import LanguageMenu from "../LanguageMenu";
import {
    URI_ADMIN_BIKE_POST,
    URI_ADMIN_STORE,
    URI_IDX_1,
} from "../../constants/URIs";
import {URL_ADMIN_STORE, URL_IDX_1} from "../../constants/URLs";
import {ERR_FETCHING_STORE} from "../../constants/messages/ErrorMessages";


/**
 * Handles the bike registration page from the admin perspective.
 *
 * @returns {Element}
 * @constructor
 */
const BikeRegistration = () =>  {
    const { t } = useTranslation('translations/admin/BikeRegistration');
    const navigate = useNavigate();
    const params = useParams();
    const token = useLocation().state.token;

    const [store, setStore] = useState('');
    const [name, setName] = useState('');
    const [image, setImage] = useState();
    const [description, setDescription] = useState('');

    const fetchStore = () => {
        fetch(URI_ADMIN_STORE.replace(URI_IDX_1, params.idx1), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`,
            }
        })
            .then(response => response.json())
            .then(data => {
                setStore(data);
            })
            .catch(error => {
                console.error(ERR_FETCHING_STORE, error);
            });
    }
    useEffect(() => {
        fetchStore();
    }, []);


    const addBike = () => {

        let formData = new FormData();

        formData.append('name', name);
        formData.append('description', description);
        formData.append('image', image)

        fetch(URI_ADMIN_BIKE_POST.replace(URI_IDX_1, store.id), {
            method: 'POST',
            headers: {
                'Authorization': `Token ${token}`
            },
            body: formData
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    // If the request was not successful throw an error
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    const handleNameInput = (value) => {
        setName(value);
    }

    const handleImageInput = (value) => {
        setImage(value)
    }

    const handleDescriptionInput = (value) => {
        setDescription(value)
    }

    const handleAddClick = () => {
        if (image !== undefined) {
            addBike();
            startTransition(() => {
                navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX_1, store.id), {state: {token: token}})
            })
        } else {
            alert(t('select_a_bike_image'))
        }
    }

    const handleCancelClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX_1, store.id), {state: {token: token}})
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX_1, store.id), {state: {token: token}})
        })
    }

    return (

        <Suspense fallback={<Loading />}>
            <div className={"view-container"}>

                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('heading')}</h1>

                <div className={"content-container"}>
                    <h3>{t('name')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => handleNameInput(e.target.value)}
                    />

                    <h3>{t('image')}</h3>
                    <input
                        type='file'
                        onInput={e => handleImageInput(e.target.files[0])}
                    />

                    <h3>{t('description')}</h3>
                    <input
                        className={'large-text-form'}
                        type='text'
                        onInput={e => handleDescriptionInput(e.target.value)}
                    />

                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleAddClick}>{t('add_bike')}</Button>
                        <Button className={'button'} onClick={handleCancelClick}>{t('cancel')}</Button>
                    </div>

                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default BikeRegistration;