import { Button } from "antd";
import '../../Style.css';
import logo_img from "../../logo.png";
import React, {startTransition, Suspense, useEffect, useState} from "react";
import { useTranslation } from "react-i18next";
import Loading from "../Loading";
import LanguageMenu from "../LanguageMenu";
import {useLocation, useNavigate} from "react-router-dom";
import {
    URI_USER_ACCOUNT_DELETE,
    URI_USER_DATA,
    URI_USER_CREDENTIALS_UPDATE,
    URI_USER_DATA_UPDATE,
} from "../../constants/URIs";
import {
    ERR_FETCHING_USER,
    ERR_UPDATING_USER,
    ERR_UPDATING_PASSWORD, ERR_DELETING_ACCOUNT
} from "../../constants/messages/ErrorMessages";
import {URL_HOME, URL_MENU} from "../../constants/URLs";

/**
 * Handles the page for changing of personal data and password by a user.
 *
 * @returns {Element}
 * @constructor
 */
const AccountManagement = () => {
    const navigate = useNavigate();

    const { t } = useTranslation('translations/user/AccountManagement');

    const token = useLocation().state.token;

    const [openPersonalDataEdit, setOpenPersonalDataEdit] = useState(false);
    const [openPasswordEdit, setOpenPasswordEdit] = useState(false);
    const [openAccountDeleteConfirmation, setOpenAccountDeleteConfirmation] = useState(false);

    const [oldUsername, setOldUsername] = useState('');
    const [oldContactData, setOldContactData] = useState('');

    const [newUsername, setNewUsername] = useState('')
    const [newContactData, setNewContactData] = useState('');
    const [password, setPassword] = useState('');


    // GET user
    const fetchUserData = () => {
        fetch(URI_USER_DATA, {
            headers: {
                'Authorization': `Token ${token}`
            }
        })
        .then(response => response.json())
        .then(data => {
            setOldUsername(data.username);
            setOldContactData(data.contact_data);
        })
        .catch(error => { console.error(ERR_FETCHING_USER, error);});
    };
    useEffect(() => {
        fetchUserData();
    });

    // PATCH user
    const patchUserData = (dataToUpdate) => {
        fetch(URI_USER_DATA_UPDATE, {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(dataToUpdate),
        })
            .then((response) => response.json())
            .catch((error) => { console.error(ERR_UPDATING_USER, error);});
    };

    // PATCH password
    const patchPassword = (dataToUpdate) => {
        fetch(URI_USER_CREDENTIALS_UPDATE, {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(dataToUpdate),
        })
            .then((response) => response.json())
            .catch((error) => {
                console.error(ERR_UPDATING_PASSWORD, error);
            });
    };

    // POST delete account
    const postAccountDeletion = () => {
        return fetch(URI_USER_ACCOUNT_DELETE, {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        });
    };

    const updatePersonalData = () => {

        let data = {};

        if (newUsername !== '') {
            data.username = newUsername;
        }

        if (newContactData !== '') {
            data.contact_data = newContactData;
        }

        patchUserData(data);
    }

    const updatePassword = () => {
        patchPassword(password);
    }

    const handlePersonalDataSubmitClick = () => {
        updatePersonalData();
        setOpenPersonalDataEdit(false);
        window.location.reload();
    }

    const handlePasswordSubmitClick = () => {
        updatePassword();
        setOpenPasswordEdit(false);
        window.location.reload();
    }

    const handleEditPersonalDataClick = () => {
        setOpenPersonalDataEdit(true)
    }

    const handleEditPasswordClick = () => {
        setOpenPasswordEdit(true);
    }

    const handleCancelPersonalDataChange = () => {
        setOpenPersonalDataEdit(false);
    }

    const handleCancelPasswordChange = () => {
        setOpenPasswordEdit(false);
    }

    const handleDeleteAccountClick = () => {
        setOpenAccountDeleteConfirmation(true)
    }

    const handleCancelAccountDeleteClick = () => {
        setOpenAccountDeleteConfirmation(false);
    }

    const handleConfirmDeleteAccountClick = () => {
        postAccountDeletion()
            .then(response => {
                if (response.ok) {
                    // Account deleted successfully
                    alert(t('account_deleted'));
                    startTransition(() => {
                        navigate(URL_HOME);
                    });
                } else {
                    // Unsuccessful account deletion
                    throw new Error(ERR_DELETING_ACCOUNT)
                }
            })
            .catch(error => {
                alert(error);
            });
    };

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}});
        });
    }

    return (

        <Suspense fallback={<Loading />}>
            <div className={"view-container"}>
                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('heading')}</h1>

                {/*only show on edit personal data*/}

                { openPersonalDataEdit ?
                    <div className={"content-container"}>
                        <h2>{t('change_personal_data')}</h2>

                        <h3>{t('username')}</h3>
                        <input
                            className={"text-form"}
                            type="text"
                            placeholder={oldUsername}
                            onInput={e => setNewUsername(e.target.value)}
                        />

                        <h3>{t('email')}</h3>
                        <input
                            className={"text-form"}
                            type="text"
                            placeholder={oldContactData}
                            onInput={e => setNewContactData(e.target.value)}
                        />
                        <div className={"button-list"}>
                            <Button className={"accent-button"} onClick={handlePersonalDataSubmitClick}>{t('submit_changes')}</Button>
                            <Button className={"button"} onClick={handleCancelPersonalDataChange}>{t('cancel')}</Button>
                        </div>
                    </div>
                    : null
                }

                { !openPersonalDataEdit && !openPasswordEdit && !openAccountDeleteConfirmation ?
                    <div className={"content-container"}>
                        <h2>{t('personal_data')}</h2>

                        <h3>{t('username')}</h3>
                        <p>{oldUsername}</p>

                        <h3>{t('email')}</h3>
                        <p>{oldContactData}</p>
                    </div>
                    : null
                }

                {/*only show on edit password*/}

                { openPasswordEdit ?

                    <div className={"content-container"}>
                        <h2>{t('change_password')}</h2>
                        <h3>{t('new_password')}</h3>
                        <input
                            className={"text-form"}
                            type="password"
                            onInput={e => setPassword(e.target.value)}
                        />
                        <div className={"button-list"}>
                            <Button className={"accent-button"} onClick={handlePasswordSubmitClick}>{t('submit_changes')}</Button>
                            <Button className={"button"} onClick={handleCancelPasswordChange}>{t('cancel')}</Button>
                        </div>
                    </div>
                    : null
                }

                { openAccountDeleteConfirmation ?

                    <div className={"content-container"}>
                        <h2>{t('delete_account_inquire')}</h2>
                        <div className={"button-list"}>
                            <Button className={"accent-button"} onClick={handleCancelAccountDeleteClick}>{t('cancel')}</Button>
                            <Button className={"button"} onClick={handleConfirmDeleteAccountClick}>{t('confirm_delete_account')}</Button>
                        </div>
                    </div>
                    : null
                }

                { !openPersonalDataEdit && !openPasswordEdit && !openAccountDeleteConfirmation ?
                    <div className={"button-list"}>
                        <Button className={"button"} onClick={handleEditPersonalDataClick}>{t('change_personal_data')}</Button>
                        <Button className={"button"} onClick={handleEditPasswordClick}>{t('change_password')}</Button>
                        <Button className={"button"} onClick={handleDeleteAccountClick}>{t('delete_account')}</Button>
                        <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                    </div>
                    : null
                }

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default AccountManagement;
