import '../../Style.css';
import {Button, TimePicker} from 'antd';
import dayjs from 'dayjs';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_ADMIN_BIKES,
    URI_ADMIN_STORE_DELETE,
    URI_ADMIN_STORE, URI_ADMIN_STORE_UPDATE,
    URI_BOOKING_REGIONS,
    URI_IDX_1, URI_HOST
} from '../../constants/URIs';
import {
    ERR_DELETING_STORE,
    ERR_FETCHING_BIKE, ERR_FETCHING_REGIONS,
    ERR_FETCHING_STORE,
    ERR_UPDATING_STORE
} from '../../constants/messages/ErrorMessages';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import Select from 'react-select';
import {
    URL_ADMIN_BIKE,
    URL_ADMIN_BIKE_REGISTRATION,
    URL_ADMIN_STORE_MANAGEMENT,
    URL_IDX_1
} from "../../constants/URLs";

/**
 * Handles a store configuration page from admin perspective.
 *
 * @returns {Element}
 * @constructor
 */
const Store = () => {
    const { t } = useTranslation('translations/admin/Store');
    const params = useParams();
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [store, setStore] = useState('');

    const [address, setAddress] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');

    const [monFrom, setMonFrom] = useState('');
    const [monTo, setMonTo] = useState('');
    const [monClosed, setMonClosed] = useState(false);

    const [tueFrom, setTueFrom] = useState('');
    const [tueTo, setTueTo] = useState('');
    const [tueClosed, setTueClosed] = useState(false);

    const [wedFrom, setWedFrom] = useState('');
    const [wedTo, setWedTo] = useState('');
    const [wedClosed, setWedClosed] = useState(false);

    const [thuFrom, setThuFrom] = useState('');
    const [thuTo, setThuTo] = useState('');
    const [thuClosed, setThuClosed] = useState(false);

    const [friFrom, setFriFrom] = useState('');
    const [friTo, setFriTo] = useState('');
    const [friClosed, setFriClosed] = useState(false);

    const [satFrom, setSatFrom] = useState('');
    const [satTo, setSatTo] = useState('');
    const [satClosed, setSatClosed] = useState(false);

    const [sunFrom, setSunFrom] = useState('');
    const [sunTo, setSunTo] = useState('');
    const [sunClosed, setSunClosed] = useState(false);

    const [prepTime, setPrepTime] = useState('');

    const [openEdit, setOpenEdit] = useState(false);

    const [allRegions, setAllRegions] = useState([]);
    const [region, setRegion] = useState('');
    const [selectedRegionOption, setSelectedRegionOption] = useState({});

    const [bikes, setBikes] = useState([]);

    // GET all regions
    const fetchAllRegions = () => {
        fetch(URI_BOOKING_REGIONS, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(response => {
                return response.json()
            })
            .then(data => {
                setAllRegions(data)
            })
            .catch(error => {
                console.error(ERR_FETCHING_REGIONS, error);
            });
    }

    const allRegionOptions = allRegions.map((region) => (
        {value: region[0], label: region[1]}
    ));


    // GET store
    const fetchStore = () => {
        fetch(URI_ADMIN_STORE.replace(URI_IDX_1, params.idx1), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`,
            }
        })
            .then(response =>  response.json())
            .then(data => {
                setStore(data);

                setMonFrom(formatStringToDate(data.mon_open));
                setMonTo(formatStringToDate(data.mon_close));
                setMonClosed(!data.mon_opened);

                setTueFrom(formatStringToDate(data.tue_open));
                setTueTo(formatStringToDate(data.tue_close));
                setTueClosed(!data.tue_opened);

                setWedFrom(formatStringToDate(data.wed_open));
                setWedTo(formatStringToDate(data.wed_close));
                setWedClosed(!data.wed_opened);

                setThuFrom(formatStringToDate(data.thu_open));
                setThuTo(formatStringToDate(data.thu_close));
                setThuClosed(!data.thu_opened);

                setFriFrom(formatStringToDate(data.fri_open));
                setFriTo(formatStringToDate(data.fri_close));
                setFriClosed(!data.fri_opened);

                setSatFrom(formatStringToDate(data.sat_open));
                setSatTo(formatStringToDate(data.sat_close));
                setSatClosed(!data.sat_opened);

                setSunFrom(formatStringToDate(data.sun_open));
                setSunTo(formatStringToDate(data.sun_close));
                setSunClosed(!data.sun_opened);

                setPrepTime(data.prep_time);

                fetchBikes(data);
            })
            .catch(error => {
                console.error(ERR_FETCHING_STORE, error);
            });
    }

    // GET bikes
    const fetchBikes = (store) => {
        fetch(URI_ADMIN_BIKES, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                setBikes(data.filter((bike) => bike.store === store.id));
            })
            .catch(error => {
                console.error(ERR_FETCHING_BIKE, error);
            });
    }

    useEffect(() => {
        fetchAllRegions();
        fetchStore();
        setSelectedRegionOption(allRegionOptions.find(option => option.value === store.region)?.label)
    }, []);

    const formatStringToDate = (string) => {
        return dayjs(string, 'HH:mm');
    }

    const formatDateToTimeString = (date) => {
        return new Date(date).getHours().toString().padStart(2, '0')
            + ':'
            + new Date(date).getMinutes().toString().padStart(2, '0');
    }

    const getRegionOptionByValue = (value) => {
        return allRegionOptions.find(option => option.value === value)?.label;
    }

    const handleEditClick = () => {
        setOpenEdit(true);
    }

    const handleRegionChange = (option) => {
        setSelectedRegionOption(option)
        setRegion(option.value)
    };

    const handleMonClosedChange = () => {
        setMonClosed(!monClosed);
        if (!monClosed) {
            setMonFrom(null);
            setMonTo(null);
        }
    }

    const handleTueClosedChange = () => {
        setTueClosed(!tueClosed);
        if (!tueClosed) {
            setTueFrom(null);
            setTueTo(null);
        }
    }

    const handleWedClosedChange = () => {
        setWedClosed(!wedClosed);
        if (!wedClosed) {
            setWedFrom(null);
            setWedTo(null);
        }
    }

    const handleThuClosedChange = () => {
        setThuClosed(!thuClosed);
        if (!thuClosed) {
            setThuFrom(null);
            setThuTo(null);
        }
    }

    const handleFriClosedChange = () => {
        setFriClosed(!friClosed);
        if (!friClosed) {
            setFriFrom(null);
            setFriTo(null);
        }
    }

    const handleSatClosedChange = () => {
        setSatClosed(!satClosed);
        if (!satClosed) {
            setSatFrom(null);
            setSatTo(null);
        }
    }

    const handleSunClosedChange = () => {
        setSunClosed(!sunClosed);
        if (!sunClosed) {
            setSunFrom(null);
            setSunTo(null);
        }
    }

    // PATCH store
    const updateStore = () => {
        const payload = {};

        if (address !== '') {
            payload.address = address;
        }
        if (region !== '') {
            payload.region = region;
        }
        if (email !== '') {
            payload.email = email;
        }
        if (phone !== '') {
            payload.phone = phone;
        }

        payload.mon_opened = !monClosed;
        if (monFrom !== '') {
            payload.mon_open = formatDateToTimeString(monFrom);
        }
        if (monFrom !== '') {
            payload.mon_close = formatDateToTimeString(monTo);
        }

        payload.tue_opened = !tueClosed;
        if (monFrom !== '') {
            payload.tue_open = formatDateToTimeString(tueFrom);
        }
        if (monFrom !== '') {
            payload.tue_close = formatDateToTimeString(tueTo);
        }

        payload.wed_opened = !wedClosed;
        if (monFrom !== '') {
            payload.wed_open = formatDateToTimeString(wedFrom);
        }
        if (monFrom !== '') {
            payload.wed_close = formatDateToTimeString(wedTo);
        }

        payload.thu_opened = !thuClosed;
        if (monFrom !== '') {
            payload.thu_open = formatDateToTimeString(thuFrom);
        }
        if (monFrom !== '') {
            payload.thu_close = formatDateToTimeString(thuTo);
        }

        payload.fri_opened = !friClosed;
        if (monFrom !== '') {
            payload.fri_open = formatDateToTimeString(friFrom);
        }
        if (monFrom !== '') {
            payload.fri_close = formatDateToTimeString(friTo);
        }

        payload.sat_opened = !satClosed;
        if (monFrom !== '') {
            payload.sat_open = formatDateToTimeString(satFrom);
        }
        if (monFrom !== '') {
            payload.sat_close = formatDateToTimeString(satTo);
        }

        payload.sun_opened = !sunClosed;
        if (monFrom !== '') {
            payload.sun_open = formatDateToTimeString(sunFrom);
        }
        if (monFrom !== '') {
            payload.sun_close = formatDateToTimeString(sunTo);
        }

        if (prepTime !== '') {
            payload.prep_time = prepTime;
        }

        fetch(URI_ADMIN_STORE_UPDATE.replace(URI_IDX_1, params.idx1), {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .then((response) => response.json())
            .catch((error) => { console.error(ERR_UPDATING_STORE, error);});
    }

    const handleConfirmEditClick = () => {
        updateStore();
        setOpenEdit(false);
        window.location.reload();
    }

    const handleCancelEditClick = () => {
        setOpenEdit(false);
    }

    // POST store deletion
    const postStoreDeletion = () => {
        return fetch(URI_ADMIN_STORE_DELETE.replace(URI_IDX_1, params.idx1), {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        });
    }

    const handleDeleteClick = () => {
        postStoreDeletion()
            .then(response => {
                if (response.ok) {
                    // Store deleted successfully
                    alert(t('store_deleted'));
                    startTransition(() => {
                        navigate(URL_ADMIN_STORE_MANAGEMENT, { state: {token: token}})
                    })
                } else {
                    // Unsuccessful store deletion
                    alert(ERR_DELETING_STORE);
                }
            })
            .catch(error => {
                console.error(ERR_DELETING_STORE, error);
            });
    }

    const handleAddBikeClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_BIKE_REGISTRATION.replace(':', '').replace(URL_IDX_1, store.id), {
                state: {token: token}
            });
        })
    }

    const handleBikeClick = (bikeId) => {
        startTransition(() => {
            navigate(URL_ADMIN_BIKE.replace(':', '').replace(URL_IDX_1, bikeId), {
                state: {token: token}
            });
        });
    }

    const BikeList = () => {
        if (bikes.length === 0) {
            return <p>{t('no_bikes')}</p>;
        }

        return (
            <>
                {bikes.map((bike) => (
                    <div
                        key={bike.id}
                        className='clickable-content-container'
                        onClick={() => handleBikeClick(bike.id)}
                    >
                        <h2>{bike.name}</h2>
                        <img className={"img"} src={URI_HOST + bike.image} alt='BikeImage'/>
                    </div>
                ))}
            </>
        );
    };

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE_MANAGEMENT, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}: {store.name}</h1>

                { openEdit ?
                    <>

                        <div className={'content-container'}>
                            <h2>{t('store_info')}</h2>

                            <h3>{t('address')}</h3>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={store.address}
                                onInput={e => setAddress(e.target.value)}
                            />


                            <h3>{t('select_region')}:</h3>
                            <Select
                                placeholder={getRegionOptionByValue(store.region)}
                                options={allRegionOptions}
                                value={selectedRegionOption}
                                name='regions'
                                onChange={handleRegionChange}
                                className='basic-multi-select'
                                classNamePrefix='select'
                            />

                            <h3>{t('email')}</h3>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={store.email}
                                onInput={e => setEmail(e.target.value)}
                            />

                            <h3>{t('phone')}</h3>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={store.phone_number}
                                onInput={e => setPhone(e.target.value)}
                            />
                        </div>

                        <div className={'content-container'}>
                            <h2>{t('opening_hours')}</h2>

                            <h3>{t('mon')}</h3>
                            {t('from')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={monFrom}
                                        onChange={(value) => setMonFrom(value)}>
                            </TimePicker>&emsp;
                            {t('to')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={monTo}
                                        onChange={(value) => setMonTo(value)}>
                            </TimePicker>&emsp;
                            {t('closed')}
                            <input type='checkbox'
                                   checked={monClosed}
                                   onChange={handleMonClosedChange}/>

                            <h3>{t('tue')}</h3>
                            {t('from')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={tueFrom}
                                        onChange={(value) => setTueFrom(value)}>
                            </TimePicker>&emsp;
                            {t('to')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={tueTo}
                                        onChange={(value) => setTueTo(value)}>
                            </TimePicker>&emsp;
                            {t('closed')}
                            <input type='checkbox'
                                   checked={tueClosed}
                                   onChange={handleTueClosedChange}/>

                            <h3>{t('wed')}</h3>
                            {t('from')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={wedFrom}
                                        onChange={(value) => setWedFrom(value)}>
                            </TimePicker>&emsp;
                            {t('to')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={wedTo}
                                        onChange={(value) => setWedTo(value)}>
                            </TimePicker>&emsp;
                            {t('closed')}
                            <input type='checkbox'
                                   checked={wedClosed}
                                   onChange={handleWedClosedChange}/>

                            <h3>{t('thu')}</h3>
                            {t('from')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={thuFrom}
                                        onChange={(value) => setThuFrom(value)}>
                            </TimePicker>&emsp;
                            {t('to')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={thuTo}
                                        onChange={(value) => setThuTo(value)}>
                            </TimePicker>&emsp;
                            {t('closed')}
                            <input type='checkbox'
                                   checked={thuClosed}
                                   onChange={handleThuClosedChange}/>

                            <h3>{t('fri')}</h3>
                            {t('from')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={friFrom}
                                        onChange={(value) => setFriFrom(value)}>
                            </TimePicker>&emsp;
                            {t('to')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={friTo}
                                        onChange={(value) => setFriTo(value)}>
                            </TimePicker>&emsp;
                            {t('closed')}
                            <input type='checkbox'
                                   checked={friClosed}
                                   onChange={handleFriClosedChange}/>

                            <h3>{t('sat')}</h3>
                            {t('from')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={satFrom}
                                        onChange={(value) => setSatFrom(value)}>
                            </TimePicker>&emsp;
                            {t('to')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={satTo}
                                        onChange={(value) => setSatTo(value)}>
                            </TimePicker>&emsp;
                            {t('closed')}
                            <input type='checkbox'
                                   checked={satClosed}
                                   onChange={handleSatClosedChange}/>

                            <h3>{t('sun')}</h3>
                            {t('from')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={sunFrom}
                                        onChange={(value) => setSunFrom(value)}>
                            </TimePicker>&emsp;
                            {t('to')}&emsp;
                            <TimePicker className={'time-picker'}
                                        placeholder={t('time')}
                                        format='HH:mm'
                                        value={sunTo}
                                        onChange={(value) => setSunTo(value)}>
                            </TimePicker>&emsp;
                            {t('closed')}
                            <input type='checkbox'
                                   checked={sunClosed}
                                   onChange={handleSunClosedChange}/>


                        </div>
                        <div className={'content-container'}>
                            <h2>{t('prep_time')}</h2>
                            {t('prep_time_in_hours')}:&emsp;
                            <input
                                className={'text-form'}
                                type='text'
                                pattern='[0-9]*'
                                value={prepTime}
                                onChange={e => setPrepTime(e.target.value)}>
                            </input>
                        </div>
                        <div className={'button-list'}>
                            <Button className={'accent-button'} onClick={handleConfirmEditClick}>{t('confirm_changes')}</Button>
                            <Button className={'button'} onClick={handleCancelEditClick}>{t('cancel')}</Button>
                        </div>
                    </>
                    :
                    <>
                        <div className={'content-container'}>
                            <h2>{t('contact_data')}</h2>
                            <p>{t('address')}: {store.address}</p>
                            <p>{t('region')}: {getRegionOptionByValue(store.region)}</p>
                            <p>{t('email')}: {store.email}</p>
                            <p>{t('phone')}: {store.phone_number}</p>

                            <h2>{t('opening_hours')}</h2>

                            { monClosed ?
                                <p>{t('mon')}: {t('closed')}</p>
                                :
                                <p>{t('mon')}: {formatDateToTimeString(monFrom)} - {formatDateToTimeString(monTo)}</p>
                            }

                            { tueClosed ?
                                <p>{t('tue')}: {t('closed')}</p>
                                :
                                <p>{t('tue')}: {formatDateToTimeString(tueFrom)} - {formatDateToTimeString(tueTo)}</p>
                            }

                            { wedClosed ?
                                <p>{t('wed')}: {t('closed')}</p>
                                :
                                <p>{t('wed')}: {formatDateToTimeString(wedFrom)} - {formatDateToTimeString(wedTo)}</p>
                            }

                            { thuClosed ?
                                <p>{t('thu')}: {t('closed')}</p>
                                :
                                <p>{t('thu')}: {formatDateToTimeString(tueFrom)} - {formatDateToTimeString(tueTo)}</p>
                            }

                            { friClosed ?
                                <p>{t('fri')}: {t('closed')}</p>
                                :
                                <p>{t('fri')}: {formatDateToTimeString(friFrom)} - {formatDateToTimeString(friTo)}</p>
                            }

                            { satClosed ?
                                <p>{t('sat')}: {t('closed')}</p>
                                :
                                <p>{t('sat')}: {formatDateToTimeString(satFrom)} - {formatDateToTimeString(satTo)}</p>
                            }

                            { sunClosed ?
                                <p>{t('sun')}: {t('closed')}</p>
                                :
                                <p>{t('sun')}: {formatDateToTimeString(sunFrom)} - {formatDateToTimeString(sunTo)}</p>
                            }


                            <h2>{t('prep_time')}</h2>
                            <p>{prepTime}</p>

                            <div className={'button-list'}>
                                <Button className={'accent-button'} onClick={handleEditClick}>{t('edit_store')}</Button>
                                <Button className={'button'} onClick={handleDeleteClick}>{t('delete_store')}</Button>
                                <Button className={'button'} onClick={handleAddBikeClick}>{t('add_bike')}</Button>
                            </div>
                        </div>
                        <BikeList />
                    </>
                }

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Store;