import React, {startTransition, Suspense, useEffect, useState} from 'react';
import 'react-calendar/dist/Calendar.css';
import '../../Style.css';
import {Button} from 'antd';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_MANAGER_BIKE,
    URI_MANAGER_BIKE_UPDATE,
    URI_MANAGER_BIKE_DELETE,
    URI_IDX_1, URI_MANAGER_EQUIPMENT, URI_HOST, URI_MANAGER_ALL_EQUIPMENT,
} from '../../constants/URIs';
import {URL_MANAGER_BIKE_MANAGEMENT, URL_MENU} from "../../constants/URLs";
import {
    ERR_DELETING_BIKE, ERR_DELETING_EQUIPMENT, ERR_FETCHING_BIKE, ERR_FETCHING_EQUIPMENT, ERR_POSTING_EQUIPMENT,
    ERR_UPDATING_BIKE,
} from "../../constants/messages/ErrorMessages";
import Select from "react-select";

/**
 * Handles the bike configuration page from the manager perspctive.
 *
 * @returns {Element}
 * @constructor
 */
const Bike = () =>  {
    const { t } = useTranslation('translations/manager/Bike');
    const params = useParams();
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [bike, setBike] = useState('');

    const [name, setName] = useState('');
    const [image, setImage] = useState();
    const [description, setDescription] = useState('');

    const [openEdit, setOpenEdit] = useState(false);
    const [openDeleteConfirmation, setOpenDeleteConfirmation] = useState(false);

    const [openAddExistingEquipment, setOpenAddExistingEquipment] = useState(true);
    const [openDeleteEquipment, setOpenDeleteEquipment] = useState(false);

    const [allEquipmentOptions, setAllEquipmentOptions] = useState([])
    const [bikeEquipmentOptions, setBikeEquipmentOptions] = useState();
    const [bikeEquipment, setBikeEquipment] = useState([]);
    const [newEquipment, setNewEquipment] = useState({});
    const [toDeleteEquipmentOptions, setToDeleteEquipmentOptions] = useState([]);
    const [toDeleteEquipment, setToDeleteEquipment] = useState([]);

    // GET all equipment
    const fetchAllEquipment = () => {
        fetch(URI_MANAGER_ALL_EQUIPMENT, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                setAllEquipmentOptions(data.map((equipmentOption) => (
                    {value: equipmentOption.id, label: equipmentOption.equipment}
                )));
            })
            .catch(error => { console.error(ERR_FETCHING_EQUIPMENT, error); });
    }

    // GET bike
    const fetchBike = () => {
        fetch(URI_MANAGER_BIKE.replace(URI_IDX_1, params.idx1), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                setBike(data);
                setBikeEquipmentOptions(data.equipment.map((equipment) => (
                    {value: equipment.id, label: equipment.equipment}
                )));
            })
            .catch(error => { console.error(ERR_FETCHING_BIKE, error); });
    };

    useEffect(() => {
        fetchAllEquipment();
        fetchBike();
    }, []);

    // POST bike equipment
    const postEquipment = (equipment) => {
        let payload = {
            equipment: equipment
        }
        fetch(URI_MANAGER_EQUIPMENT.replace(URI_IDX_1, params.idx1), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .catch((error) => {
                console.error(ERR_POSTING_EQUIPMENT, error);
            });
    }

    // DELETE equipment
    const deleteEquipment = (equipment) => {
        let payload = {
            equipment: equipment
        }
        fetch(URI_MANAGER_EQUIPMENT.replace(URI_IDX_1, params.idx1), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .catch((error) => {
                console.error(ERR_DELETING_EQUIPMENT, error);
            });
    }

    // PATCH bike info
    const updateBike = () => {

        let formData = new FormData();

        if (name !== '') {
            formData.append('name', name);
        }

        if (image !== undefined) {
            formData.append('image', image)
        }

        if (description !== '') {
            formData.append('description', description);
        }

        fetch(URI_MANAGER_BIKE_UPDATE.replace(URI_IDX_1, params.idx1), {
            method: 'PATCH',
            headers: {
                'Authorization': `Token ${token}`
            },
            body: formData,
        })
            .then((response) => { response.json(); })
            .catch((error) => {
                console.error(ERR_UPDATING_BIKE, error);
            });
    }

    // DELETE bike
    const postBikeDeletion = () => {
        return fetch(URI_MANAGER_BIKE_DELETE.replace(URI_IDX_1, params.idx1), {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        });
    };

    const handleEditClick = () => {
        setOpenEdit(true);
    }

    const handleSelectedEquipmentChange = (selectedOptions) => {
        setBikeEquipmentOptions(selectedOptions);
        setBikeEquipment(selectedOptions.map(option => option.label))
    }

    const handleDeleteClick = () => {
        setOpenDeleteConfirmation(true);
    }

    const handleConfirmDeleteClick = () => {
        postBikeDeletion()
            .then(response => {
                if (response.ok) {
                    // Bike deleted successfully
                    alert(t('bike_deleted'));
                    startTransition(() => {
                        navigate(URL_MANAGER_BIKE_MANAGEMENT, {state: {token: token}})
                    })
                } else {
                    // Unsuccessful bike deletion
                    console.error(ERR_DELETING_BIKE);
                }
            })
            .catch(error => {
                console.error(ERR_DELETING_BIKE, error);
            });
    }

    const handleCancelDeleteClick = () => {
        setOpenDeleteConfirmation(false);
    }

    const handleAddNewEquipmentClick = () => {
        setBikeEquipmentOptions([]);
        setOpenAddExistingEquipment(false);
    }

    const handleAddExistingEquipmentClick = () => {
        setNewEquipment('');
        setOpenAddExistingEquipment(true);
    }

    const handleDeleteEquipmentClick = () => {
        setOpenDeleteEquipment(true);
    }

    const handleToDeleteEquipmentChange = (selectedOptions) => {
        setToDeleteEquipmentOptions(selectedOptions);
        setToDeleteEquipment(selectedOptions.map(option => option.label))
    }

    const handleConfirmDeleteEquipmentClick = () => {
        if (toDeleteEquipment !== []) {
            deleteEquipment();
        }
        setOpenDeleteEquipment(false);
        window.location.reload();
    }

    const handleCancelDeleteEquipmentClick = () => {
        setOpenDeleteEquipment(false);
    }

    const handleSubmitClick = () => {
        updateBike();
        if (bikeEquipment.length !== 0) {
            bikeEquipment.forEach(equipment => postEquipment(equipment))
        } else if (newEquipment !== '') {
            postEquipment(newEquipment);
        }
        setOpenEdit(false);
        window.location.reload();
    }

    const handleCancelClick = () => {
        setOpenEdit(false);
    }

    const handleNameEdit = (e) => {
        setName(e.target.value);
    }

    const handleImageEdit = (e) => {
        setImage(e.target.files[0])
    }

    const handleDescriptionEdit = (e) => {
        setDescription(e.target.value);
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}});
        })
    }

    return (

        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>

                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                { openEdit ?
                    <>
                        <div className={'content-container'}>

                            <h2>{t('bike_info')}</h2>

                            <h3>{t('name')}</h3>
                            <input
                                className={"text-form"}
                                type="text"
                                placeholder={bike.name}
                                onInput={e => handleNameEdit(e)}
                            />

                            <h3>{t('image')}</h3>
                            <div>
                                <img className={'img'} src={URI_HOST + bike.image} alt='BikeImage' width={'100%'}/>
                            </div>
                            <input
                                type="file"
                                onInput={e => handleImageEdit(e)}
                            />

                            <h3>{t('description')}</h3>
                            <input
                                className={"text-form"}
                                type="text"
                                value={bike.description}
                                onInput={e => handleDescriptionEdit(e)}
                            />
                        </div>
                        <div className={'content-container'}>
                            <h2>{t('equipment')}</h2>

                            { bike.equipment?.length !== 0 ?
                                <p>{bike.equipment?.map(e => e.equipment).join(', ')}</p>
                                :
                                <p>{t('none')}</p>
                            }

                            { openDeleteEquipment ?
                                <>
                                    <h3>{t('delete_equipment')}</h3>
                                    <Select
                                        isMulti
                                        placeholder={t('none')}
                                        options={bikeEquipmentOptions}
                                        value={toDeleteEquipmentOptions}
                                        name='equipment'
                                        onChange={handleToDeleteEquipmentChange}
                                        className='basic-multi-select'
                                        classNamePrefix='select'
                                    />
                                    <div className='button-list'>
                                        <Button className={'accent-button'} onClick={handleConfirmDeleteEquipmentClick}>{t('confirm_equipment_deletion')}</Button>
                                        <Button className={'button'} onClick={handleCancelDeleteEquipmentClick}>{t('cancel')}</Button>
                                    </div>
                                </>
                                :
                                <>
                                    { openAddExistingEquipment ?
                                        <>
                                            <h3>{t('new_equipment')}</h3>
                                            <input
                                                className={'text-form'}
                                                type='text'
                                                placeholder={t('none')}
                                                onInput={e => setNewEquipment(e.target.value)}
                                            />
                                            <div className='button-list'>
                                                <Button className={'button'} onClick={handleAddNewEquipmentClick}>{t('add_existing_equipment')}</Button>
                                            </div>
                                        </>
                                        :
                                        <>
                                            <h3>{t('existing_equipment')}</h3>
                                            <Select
                                                isMulti
                                                placeholder={t('none')}
                                                options={allEquipmentOptions}
                                                value={bikeEquipmentOptions}
                                                name='equipment'
                                                onChange={handleSelectedEquipmentChange}
                                                className='basic-multi-select'
                                                classNamePrefix='select'
                                            />
                                            <div className='button-list'>
                                                <Button className={'button'} onClick={handleAddExistingEquipmentClick}>{t('add_new_equipment')}</Button>
                                            </div>
                                        </>
                                    }

                                    <div className='button-list'>
                                        <Button className={'button'} onClick={handleDeleteEquipmentClick}>{t('delete_equipment')}</Button>
                                    </div>
                                </>
                            }
                        </div>


                        <div className={'button-list'}>
                            <Button className={'accent-button'} onClick={handleSubmitClick}>{t('submit')}</Button>
                            <Button className={'button'} onClick={handleCancelClick}>{t('cancel')}</Button>
                        </div>
                    </>
                    :
                    <>
                        <div className={'content-container'}>
                            <h2>{bike.name}</h2>
                            { bike.image ?
                                <img className={'img'} src={URI_HOST + bike.image} alt='BikeImage' width={'100%'}/>
                                :
                                null
                            }
                            <p>{bike.description}</p>

                            { bike.equipment?.length !== 0 ?
                                <p>{t('equipment')}: {bike.equipment?.map(e => e.equipment).join(', ')}</p>
                                :
                                <p>{t('equipment')}: {t('none')}</p>
                            }

                            { openDeleteConfirmation ?
                                <>
                                    <h2>{t('delete_inquire')}</h2>
                                    <div className={"button-list"}>
                                        <Button className={"accent-button"} onClick={handleCancelDeleteClick}>{t('cancel')}</Button>
                                        <Button className={"button"} onClick={handleConfirmDeleteClick}>{t('confirm_delete')}</Button>
                                    </div>
                                </>
                                :
                                <div className={'button-list'}>
                                    <Button className={'accent-button'} onClick={handleEditClick}>{t('edit')}</Button>
                                    <Button className={'button'} onClick={handleDeleteClick}>{t('delete')}</Button>
                                </div>
                            }
                        </div>
                        <div className={'button-list'}>
                            <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                        </div>
                    </>
                }
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Bike;