
import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {URI_USER_LOGIN, URI_USER_REGISTER} from "../../constants/URIs";
import {
    URL_BOOKING_CHECKOUT,
    URL_USER_HELMHOLTZ,
    URL_USER_LOGIN,
    URL_MENU
} from "../../constants/URLs";
import {useLocation, useNavigate} from "react-router-dom";
import {ERR_MAKING_REGISTER_REQUEST} from "../../constants/messages/ErrorMessages";

/**
 * Handles the sign in page.
 *
 * @returns {Element}
 * @constructor
 */
const Register = () => {
    const { t } = useTranslation('translations/user/Register');
    const navigate = useNavigate();
    const bookingData = useLocation().state?.booking_data;

    const [username, setUsername] = useState('');
    const [yearOfBirth, setYearOfBirth] = useState('');
    const [contactData, setContactData] = useState('');
    const [password, setPassword] = useState('');

    const handleUsernameInput = (e) => {
        setUsername(e.target.value);
    }

    const handleYearOfBirthInput = (e) => {
        setYearOfBirth(e.target.value);
    }

    const handleContactDataInput = (e) => {
        setContactData(e.target.value);
    }

    const handlePasswordInput = (e) => {
        setPassword(e.target.value);
    }

    // POST sign in request
    const postSignIn = () => {

        let payload = {
            contact_data: contactData,
            username: username,
            password: password,
        };

        if (yearOfBirth !== '') {
            payload.year_of_birth = yearOfBirth;
        }

        // Send the POST request to the server endpoint
        fetch(URI_USER_REGISTER, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response.ok) {
                    return response.text().then(text => {
                        if (!text.trim()) {
                            postLogin();
                        } else {
                            return JSON.parse(text);
                        }
                    });

                }
                else {
                    // If the request was not successful, throw an error
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
            .catch(error => {
                // Handle any network or other errors that occurred during the request
                alert(ERR_MAKING_REGISTER_REQUEST + ' ' + error.message);
            });
    }

    const handleSignInClick = () => {
        postSignIn();
    }

    // POST login request
    const postLogin = () => {

        let payload = {
            username: username,
            password: password
        };

        // POST login request
        fetch(URI_USER_LOGIN, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    // If the request was not successful, throw an error
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
            .then(data => {
                const token = data.token;
                navigateToNextPage(token);
            })
            .catch(error => {
                // Handle any network or other errors that occurred during the request
                alert('Error making login request: ' + error.message);
            });
    }

    const navigateToNextPage = (token) => {

        if (bookingData) {
            startTransition(() => {
                navigate(URL_BOOKING_CHECKOUT, {
                    state: {
                        token: token,
                        booking_data: bookingData
                    }
                })
            })
        } else {
            startTransition(() => {

                navigate(URL_MENU, {state: {token: token}});
            });
        }
    }

    const handeHelmholtzSignInClick = () => {
        window.location.replace(URL_USER_HELMHOLTZ);
    }

    const handleLoginClick = () => {
        startTransition(() => {
            navigate(URL_USER_LOGIN, {
                state: {
                    booking_data: bookingData
                }
            });
        })
    }

    return (

        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>

                    <h3>{t('username')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => handleUsernameInput(e)}
                    />

                    <h3>{t('year_of_birth')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => handleYearOfBirthInput(e)}
                    />

                    <h3>{t('email')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => handleContactDataInput(e)}
                    />

                    <h3>{t('password')}</h3>
                    <input
                        className={'text-form'}
                        type='password'
                        onInput={e => handlePasswordInput(e)}
                    />

                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleSignInClick}>{t('sign_in')}</Button>
                        <Button className={'button'} onClick={handeHelmholtzSignInClick}>{t('helmholtz_sign_in')}</Button>
                    </div>
                    <div className={'button-list'}>
                        <Button className={'button'} onClick={handleLoginClick}>{t('log_in')}</Button>
                    </div>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>

    );
}

export default Register;