/**
 * Max number of days that can be booked at once.
 *
 * @type {number}
 */
export const FILTER_MAX_DATES = 7;

/**
 * Number of days a user remains verified after a new/renewed verification.
 *
 * @type {number}
 */
export const VERIFICATION_TIME_LIMIT = 1000 * 60 * 60 * 24 * 180; // 180 days