
import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {useLocation, useNavigate} from "react-router-dom";
import {URI_USER_LOGIN} from "../../constants/URIs";
import {
    URL_BOOKING_CHECKOUT, URL_IDX_1,
    URL_USER_HELMHOLTZ,
    URL_MENU,
    URL_USER_REGISTER, URL_MANAGER_BOOKING
} from "../../constants/URLs";

/**
 * Handles the login page.
 *
 * @returns {Element}
 * @constructor
 */
const Login = () => {
    const { t } = useTranslation('translations/user/Login');
    const navigate = useNavigate();
    const location =  useLocation();
    const bookingData = location.state?.booking_data;
    const handoutData = location.state?.handout_data;

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');


    const handleUsernameInput = (value) => {
        setUsername(value);
    }

    const handlePasswordInput = (value) => {
        setPassword(value);
    }

    function handleLoginClick() {
        postLogin();
    }

    // POST login request
    const postLogin = () => {

        let payload = {
            username: username,
            password: password
        };

        // POST login request
        fetch(URI_USER_LOGIN, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(payload)
        })
        .then(response => {
            if (response?.ok) {
                return response.json();
            } else {
                // If the request was not successful, throw an error
                return response.json().then(errorData => {
                    throw new Error(errorData.message);
                });
            }
        })
        .then(data => {
            const token = data.token;
            navigateToNextPage(token);
        })
        .catch(error => {
            // Handle any network or other errors that occurred during the request
            alert('Error making login request.' + error.message);
        });
    }

    const handeHelmholtzSignInClick = () => {
        window.location.replace(URL_USER_HELMHOLTZ);
    }

    const handleSignInClick = () => {
        startTransition(() => {
            navigate(URL_USER_REGISTER, {
                state: {
                    booking_data: bookingData
                }
            });
        })
    }

    const navigateToNextPage = (token) => {

        if (bookingData) {
            startTransition(() => {
                navigate(URL_BOOKING_CHECKOUT.replace(':', '').replace(URL_IDX_1, bookingData.bike), {
                    state: {
                        booking_data: bookingData,
                        token: token
                    }
                })
            })
        } else if (handoutData) {
            startTransition(() => {
                navigate(URL_MANAGER_BOOKING.replace(':', '').replace(URL_IDX_1, handoutData.string), {
                    state: {token: token}});
            })
        } else {
            startTransition(() => {
                navigate(URL_MENU, {state: {token: token}});
            });
        }
    }

    return (

        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>
                    <h3>{t('username')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => handleUsernameInput(e.target.value)}
                    />
                    <h3>{t('password')}</h3>
                    <input
                        className={'text-form'}
                        type='password'
                        onInput={e => handlePasswordInput(e.target.value)}
                    />
                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleLoginClick}>{t('login')}</Button>
                        <Button className={'button'} onClick={handeHelmholtzSignInClick}>{t('helmholtz_login')}</Button>
                    </div>
                    <div className={'button-list'}>
                        <Button className={'button'} onClick={handleSignInClick}>{t('sign_in')}</Button>
                    </div>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>

    );
}

export default Login;