
import flagDe from '../flag_images/flag_de.png';
import flagEn from '../flag_images/flag_en.png';
import i18n from "i18next";

/**
 * Implements the display language switching menu.
 *
 * @returns {JSX.Element}
 * @constructor
 */
const LanguageMenu = () => {

    const langs = i18n.languages;

    const handleGermanClick = () => {

        i18n.changeLanguage('de')
    }

    const handleEnglishClick = () => {
        i18n.changeLanguage('en')
    }

    return (

        <div className={"language_menu"}>
            <img className={"language_flag_img"} src={flagDe} alt="German" onClick={handleGermanClick}/>
            <img className={"language_flag_img"} src={flagEn} alt="English" onClick={handleEnglishClick}/>
        </div>
    );
}

export default LanguageMenu;