
import '../../Style.css';
import {Button} from "antd";
import React, {startTransition, Suspense, useState} from "react";
import logo_img from "../../logo.png";
import {useTranslation} from "react-i18next";
import Loading from "../Loading";
import LanguageMenu from "../LanguageMenu";
import {URI_MANAGER_ENROLLMENT} from "../../constants/URIs";
import {useLocation, useNavigate} from "react-router-dom";
import {URL_MENU} from "../../constants/URLs";
import {ERR_POSTING_ENROLLMENT} from "../../constants/messages/ErrorMessages";

/**
 * Handles the page of enrollment of managers by a manager..
 *
 * @returns {Element}
 * @constructor
 */
const Enrollment = () => {
    const { t } = useTranslation('translations/manager/Enrollment');
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [username, setUsername] = useState('');

    // POST new manager
    const postEnrollRequest = () => {

        let payload = {
            username: username
        };

        // POST login request
        fetch(URI_MANAGER_ENROLLMENT, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token: ${token}`
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    // If the request was not successful, throw an error
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
            .then(data => {
                alert(t('enrollment_successful'))
                navigateToNextPage();
            })
            .catch(error => {
                // Handle any network or other errors that occurred during the request
                alert(ERR_POSTING_ENROLLMENT);
            });
    }

    const navigateToNextPage = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    const handleUsernameInput = (value) => {
        setUsername(value);
    }

    const handleEnrollClick = () => {
        postEnrollRequest();
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}});
        });
    }

    return (

        <Suspense fallback={<Loading />}>

            <div className={"view-container"}>
                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('header')}</h1>
                <div className={"content-container"}>

                    <p>{t('enter_to_id')}:</p>

                    <h2>{t('username')}</h2>
                    <input
                        className={"text-form"}
                        type="text"
                        onInput={e => handleUsernameInput(e.target.value)}
                    />

                    <div className={"button-list"}>
                        <Button className={"accent-button"} onClick={handleEnrollClick}>{t('enroll_manager')}</Button>
                    </div>


                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Enrollment;