import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_ADMIN_STORE_POST,
    URI_BOOKING_REGIONS,
} from '../../constants/URIs';
import {useLocation, useNavigate} from 'react-router-dom';
import {URL_ADMIN_STORE_MANAGEMENT} from '../../constants/URLs';
import Select from 'react-select';
import {ERR_POSTING_STORE} from '../../constants/messages/ErrorMessages';

/**
 *
 *
 * @returns {Element}
 * @constructor
 */
const StoreRegistration = () => {
    const { t } = useTranslation('translations/admin/StoreRegistration');
    const navigate = useNavigate();
    const token = useLocation().state.token;


    const [name, setName] = useState('');
    const [address, setAddress] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');

    const [allRegions, setAllRegions] = useState([]);
    const [selectedRegion, setSelectedRegion] = useState('');

    const allRegionOptions = allRegions.map((region) => (
        {value: region[0], label: region[1]}
    ));

    // GET all regions
    const fetchAllRegions = () => {
        fetch(URI_BOOKING_REGIONS, {
        })
            .then(response => {
                return response.json()
            })
            .then(data => {
                setAllRegions(data)
            })
    }
    useEffect(() => {
        fetchAllRegions()
    }, [])

    const handleRegionChange = (selection) => {
        setSelectedRegion(selection.value)
    };

    // POST store
    const postStore = () => {
        const payload = {
            name: name,
            address: address,
            region: selectedRegion,
            email: email,
            phone_number: phone
        }
        fetch(URI_ADMIN_STORE_POST, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .then((response) => response.json())
            .catch((error) => { alert(ERR_POSTING_STORE + error);});
    }

    const handleRegisterClick = () => {
        postStore();
        startTransition(() => {
            navigate(URL_ADMIN_STORE_MANAGEMENT, {state: {token: token}})
        })
    }

    const handleCancelClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE_MANAGEMENT, {state: {token: token}})
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE_MANAGEMENT, {state: {token: token}})
        })
    }

    return (

        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>
                    <h2>{t('info')}</h2>
                    <h3>{t('name')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => setName(e.target.value)}
                    >
                    </input>
                    <h3>{t('address')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => setAddress(e.target.value)}
                    >
                    </input>
                    <h3>{t('email')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => setEmail(e.target.value)}
                    >
                    </input>
                    <h3>{t('phone')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => setPhone(e.target.value)}
                    >
                    </input>
                    <h3>{t('region')}</h3>
                    <div className='form-field'>
                        <Select
                            placeholder={'None'}
                            options={allRegionOptions}
                            name='regions'
                            onChange={handleRegionChange}
                            className='basic-single'
                            classNamePrefix='select'
                        />
                    </div>
                </div>
                <div className={'button-list'}>
                    <Button className={'accent-button'} onClick={handleRegisterClick}>{t('register_store')}</Button>
                    <Button className={'button'} onClick={handleCancelClick}>{t('cancel')}</Button>
                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default StoreRegistration;