import '../../Style.css';
import {Button} from 'antd';
import logo_img from '../../logo.png';
import React, {startTransition, Suspense, useEffect, useState} from 'react';

import { useTranslation } from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from "../LanguageMenu";
import {useNavigate, useParams} from "react-router-dom";
import {URI_IDX_1, URI_IDX_2, URI_USER_EMAIL_CONFIRMATION} from "../../constants/URIs";
import {URL_HOME} from "../../constants/URLs";
import {ERR_VERIFYING_USER} from "../../constants/messages/ErrorMessages";

/**
 * Handles the page which runs the verification of a user e-mail address.
 *
 * @returns {Element}
 * @constructor
 */
const EmailVerification = () => {
    const { t } = useTranslation('translations/user/EmailVerification');
    const navigate = useNavigate();
    const params = useParams();
    const id = params.idx1;
    const key = params.idx2;

    const [verificationSuccessful, setVerificationSuccessful] = useState(false);

    // POST email verification
    const postVerification = () => {
        fetch(URI_USER_EMAIL_CONFIRMATION.replace(URI_IDX_1, id).replace(URI_IDX_2, key), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(response => {
                if (response) {
                    setVerificationSuccessful(true)
                    return response.json();
                }
                else {
                    // If the request was not successful, throw an error
                    return response.json().then(error => {
                        throw new Error(error.message);
                    });
                }
            })
            .catch(error => {
                alert(ERR_VERIFYING_USER + error.message)
            })
    }

    useEffect(() => {
        postVerification()
    }, []);

    const handleOkClick = () => {
        startTransition(() => {
            navigate(URL_HOME);
        })
    }

    return (
        <div className={'view-container'}>
            <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
            <h1>{t('heading')}</h1>

            { verificationSuccessful ?
                <>
                    <h3>{t('email_verification_successful')}</h3>
                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleOkClick}>{t('continue')}</Button>
                    </div>
                </>
                :
                <>
                    <h2>{t('something_went_wrong')}</h2>
                </>
            }
            <LanguageMenu />
        </div>
    );
}

export default EmailVerification;