import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_IDX_1,
    URI_MANAGER_BIKE,
    URI_MANAGER_COMMENT_UPDATE,
    URI_HOST,
    URI_MANAGER_BOOKING_CANCEL,
    URI_MANAGER_LOCAL_DATA,
    URI_MANAGER_POST_HANDOUT,
    URI_MANAGER_POST_RETURN,
    URI_MANAGER_BOOKING_BY_QR_STRING,
    URI_MANAGER_BOOKING,
    URI_MANAGER_POST_LOCAL_DATA, URI_MANAGER_PATCH_LOCAL_DATA, URI_MANAGER_REPORT_COMMENT
} from '../../constants/URIs';
import {
    URL_BOOKING_FILTER,
    URL_IDX_1,
    URL_MANAGER_BIKE,
    URL_MANAGER_BOOKING_MANAGEMENT, URL_USER_LOGIN
} from '../../constants/URLs';
import {
    ERR_FETCHING_ASSURANCE_LVL,
    ERR_FETCHING_BIKE,
    ERR_FETCHING_BOOKING, ERR_POSTING_ACCEPTING,
    ERR_POSTING_BOOKING,
    ERR_POSTING_HANDOUT, ERR_POSTING_RETURN,
    ERR_REPORTING_COMMENT,
    ERR_UNEXPECTED_RESPONSE_STATUS,
    ERR_UPDATING_COMMENT,
    ERR_VERIFYING_USER
} from "../../constants/messages/ErrorMessages";
import {VERIFICATION_TIME_LIMIT} from "../../constants/Config";
import dayjs from "dayjs";

/**
 * Handles the info page of a booking from the manager perspective with bike handout and bike accepting functionality.
 *
 * @returns {Element}
 * @constructor
 */
const Booking = () => {
    const { t } = useTranslation('translations/manager/Booking');
    const navigate = useNavigate();
    const params = useParams();
    const token = useLocation().state?.token;

    const [booking, setBooking] = useState({});
    const [comment, setComment] = useState('');
    const [bike, setBike] = useState('');
    const [openCommentEdit, setOpenCommentEdit] = useState(false);

    const [bikePickedUp, setBikePickedUp] = useState(false);
    const [bikeBooked, setBikeBooked] = useState(false);
    const [bikeReturned, setBikeReturned] = useState(false);

    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [address, setAddress] = useState('');
    const [idNumber, setIdNumber] = useState('');

    const [userVerifiedCurrently, setUserVerifiedCurrently] = useState(false);
    const [userVerifiedBefore, setUserVerifiedBefore] = useState(false);

    // GET booking
    const fetchBookingById = () => {
        fetch(URI_MANAGER_BOOKING.replace(URI_IDX_1, params.idx1), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(booking => {
                setBooking(booking);
                setComment(booking.comment)

                fetchBike(booking.bike);
            })
            .catch(error => { console.error(ERR_FETCHING_BOOKING, error); });
    }

    // GET booking
    const fetchBookingByString = () => {
        fetch(URI_MANAGER_BOOKING_BY_QR_STRING.replace(URI_IDX_1, params.idx1), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => {
                if (response.status === 401) {
                    startTransition(() => {
                        navigate(URL_USER_LOGIN, {state: {handout_data: {string: params.idx1}}});
                    })
                } else {
                    return response.json();
                }
            })
            .then(data => {
                setBooking(data);
                if (data.booking_status.map(e => e.booking_status).includes('Picked up')) {
                    setBikePickedUp(true);
                    setBikeBooked(true);
                } else if (data.booking_status.map(e => e.booking_status).includes('Booked')) {
                    setBikeBooked(true)
                } else if (data.booking_status.map(e => e.booking_status).includes('Returned')) {
                    setBikeReturned(true);
                }
                fetchLocalData(data.id);
                fetchBike(data.bike);
            })
            .catch(error => { console.error(ERR_FETCHING_BOOKING, error); });
    }

    // GET local data
    const fetchLocalData = (bookingId) => {

        fetch(URI_MANAGER_LOCAL_DATA.replace(URI_IDX_1, bookingId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => {
                if (response.status === 404) {
                    setUserVerifiedBefore(false);
                } else if (response.status === 200) {
                    return response.json();
                } else {
                    throw new Error(ERR_UNEXPECTED_RESPONSE_STATUS + response.status);
                }
            })
            .then(localData => {
                if (localData) {
                    setUserVerifiedBefore(true);
                    setUserVerifiedCurrently(isUserVerifiedCurrently(localData.date_of_verification))
                    setFirstName(localData.first_name);
                    setLastName(localData.last_name);
                    setAddress(localData.address);
                    setIdNumber(localData.id_number);
                }
            })
            .catch(error => {
                alert(ERR_FETCHING_ASSURANCE_LVL + error);
            });
    }

    // GET bike
    const fetchBike = (bikeId) => {
        fetch(URI_MANAGER_BIKE.replace(URI_IDX_1, bikeId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setBike(data) })
            .catch(error => { console.error(ERR_FETCHING_BIKE, error); });
    };

    useEffect(() => {
        if (!token) {
            startTransition(() => {
                navigate(URL_USER_LOGIN, {state: {handout_data: {string: params.idx1}}});
            })
        }
        if (isNaN(params.idx1)) {
            fetchBookingByString()
        } else {
            fetchBookingById();
        }
    }, []);

    const isUserVerifiedCurrently = (sateString) => {
        let dateOfVerification = formatStringToDate(sateString)
        return new Date().getTime() - dateOfVerification <= VERIFICATION_TIME_LIMIT;
    }

    const formatStringToDate = (string) => {
        return dayjs(string, 'HH:mm');
    }

    // PATCH comment
    const updateComment = () => {
        const payload = {
            comment: comment
        }
        fetch(URI_MANAGER_COMMENT_UPDATE.replace(URI_IDX_1, booking.id), {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .then((response) => response.json())
            .catch((error) => { console.error(ERR_UPDATING_COMMENT, error);});
    }

    // POST comment report to admin
    const reportComment = () => {
        return fetch(URI_MANAGER_REPORT_COMMENT.replace(URI_IDX_1, booking.id), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
        });
    }

    // POST booking cancellation
    const cancelBooking = () => {
        fetch(URI_MANAGER_BOOKING_CANCEL.replace(URI_IDX_1, booking.id), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => {
                if (response.ok) {
                    return response
                } else {
                    // If the request was not successful, handle errors here
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    const handleEditCommentClick = () => {
        setOpenCommentEdit(true);
    }

    const handleSubmitCommentEditClick = () => {
        updateComment();
        setOpenCommentEdit(false);
        window.location.reload();
    }

    const handleCancelCommentEditClick = () => {
        setOpenCommentEdit(false);
    }

    const handleReportCommentClick = () => {
        reportComment()
            .then(response => {
                    if (response.ok) {
                        alert(t('comment_reported_successfully'));
                    } else {
                        throw new Error(ERR_REPORTING_COMMENT)
                    }
                })
            .catch((error) => alert(error));
    }

    const handleCancelBookingClick = () => {
        cancelBooking();
        startTransition(() => {
            navigate(URL_MANAGER_BOOKING_MANAGEMENT, {state: {token: token}})
        })
    }

    const handleBikeClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BIKE.replace(':', '').replace(URL_IDX_1, bike.id), {state: {token: token}});
        })
    }

    // POST local data for a new user
    const postLocalDataOfUser = () => {
        let payload = {
            first_name: firstName,
            last_name: lastName,
            address: address,
            id_number: idNumber
        };
        fetch(URI_MANAGER_POST_LOCAL_DATA.replace(URI_IDX_1, booking.id), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response.ok) {
                    return null
                } else {
                    throw new Error(ERR_REPORTING_COMMENT)
                }
            })
            .catch((error) => alert(error));
    }

    // PATCH local data for an old user
    const patchLocalDataOfUser = () => {
        let payload = {
            id_number: idNumber
        };
        fetch(URI_MANAGER_PATCH_LOCAL_DATA.replace(URI_IDX_1, booking.id), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    // If the request was not successful, handle errors here
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    // POST bike handout
    const postHandout = () => {
        return fetch(URI_MANAGER_POST_HANDOUT.replace(URI_IDX_1, booking.id), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        });
    }

    // POST bike return
    const postReturn = () => {
        return fetch(URI_MANAGER_POST_RETURN.replace(URI_IDX_1, booking.id), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
        });
    }

    const handleHandoutClick = () => {
        if (!userVerifiedCurrently) {
            if (userVerifiedBefore) {
                patchLocalDataOfUser()
            } else {
                postLocalDataOfUser()
            }
        }
        postHandout()
            .then(response => {
                if (response.ok) {
                    alert(t('handout_successful'));
                } else {
                    // Handle unsuccessful account deletion
                    throw new Error(ERR_POSTING_HANDOUT)
                }
            })
            .catch(error => {
                alert(error);
            });
        window.location.reload();
    }

    const handleReturnClick = () => {
        postReturn()
        .then(response => {
                if (response.ok) {
                    alert(t('accepting_successful'));
                } else {
                    // Handle unsuccessful account deletion
                    throw new Error(ERR_POSTING_ACCEPTING)
                }
            })
                .catch(error => {
                    alert(error);
                });
        window.location.reload();
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BOOKING_MANAGEMENT, {state: {token: token}});
        })
    }

    return (

        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>
                    <h2>{t('booking_data')}</h2>
                    <p>{t('preferred_username')}: {booking.preferred_username}</p>
                    <p>{t('from')}: {booking.begin}</p>
                    <p>{t('to')}: {booking.end}</p>
                    { booking.equipment?.length !== 0 ?
                        <p>{t('equipment')}: {booking.equipment?.map(e => e.equipment).join(', ')}</p>
                        :
                        <p>{t('equipment')}: {t('none')}</p>
                    }
                    <p>{t('status')}: {booking.booking_status?.map(e => e.booking_status).join(', ')}</p>
                </div>
                <div className={'content-container'}>
                    <h2>{t('comment')}</h2>
                    { openCommentEdit ?
                        <>
                            <input
                                className={'text-form'}
                                type='text'
                                value={comment}
                                onInput={e => setComment(e.target.value)}
                            />
                            <div className={'button-list'}>
                                <Button className={'accent-button' } onClick={handleSubmitCommentEditClick}>{t('submit_changes')}</Button>
                                <Button className={'button' } onClick={handleCancelCommentEditClick}>{t('cancel')}</Button>
                            </div>
                        </>
                        :
                        <>
                            { booking.comment ?
                                <p>{booking.comment}</p> : <p>{t('no_comment')}</p>
                            }
                            <div className={'button-list'}>
                                <Button className={'button' } onClick={handleEditCommentClick}>{t('edit_comment')}</Button>
                                <Button className={'button'} onClick={handleReportCommentClick}>{t('report_comment')}</Button>

                            </div>
                        </>
                    }
                </div>
                <div className={'clickable-content-container'} onClick={handleBikeClick}>
                    <h2>{bike.name}</h2>
                    <img className={'img'} src={URI_HOST + bike.image} alt='BikeImage'></img>
                </div>
                { bikeBooked ?
                    // bike not returned
                    <>
                        { userVerifiedCurrently ?
                            // user verified
                            <div className={"content-container"}>
                                <h2>{t('id_verification')}</h2>

                                <h3>{t('last_id_number_digits')}</h3>
                                <input
                                    className={"text-form"}
                                    type="text"
                                    onInput={e => { setIdNumber(e.target.value) }}
                                />
                            </div>

                            :
                            // user not verified
                            <div className={"content-container"}>
                                <h2>{t('renew_ass_lvl')}</h2>

                                <h3>{t('first_name')}</h3>
                                <input
                                    className={'text-form'}
                                    type='text'
                                    defaultValue={firstName}
                                    onInput={e => setFirstName(e.target.value)}
                                />

                                <h3>{t('last_name')}</h3>
                                <input
                                    className={'text-form'}
                                    type='text'
                                    defaultValue={lastName}
                                    onInput={e => setLastName(e.target.value)}
                                />

                                <h3>{t('address')}</h3>
                                <input
                                    className={'text-form'}
                                    type='text'
                                    defaultValue={address}
                                    onInput={e => setAddress(e.target.value)}
                                />

                                {/* user have been verified before */}
                                { userVerifiedBefore ?
                                    <>
                                        <h3>{t('last_id_number_digits')}</h3>
                                        <input
                                            className={"text-form"}
                                            type="text"
                                            onInput={e => setIdNumber(e.target.value)}
                                        />
                                    </>
                                    :
                                    // user have not been verified before
                                    <>
                                        <h3>{t('last_id_number_digits')}</h3>
                                        <input
                                            className={'text-form'}
                                            type='text'
                                            onInput={e => setIdNumber(e.target.value)}
                                        />
                                    </>
                                }
                            </div>
                        }
                        <div className={"button-list"}>
                            { bikePickedUp ?
                                // bike handed out
                                <Button className={"button"} onClick={handleReturnClick}>{t('accept')}</Button>
                                :
                                // bike not handed out yet
                                <Button className={"button"} onClick={handleHandoutClick}>{t('handout')}</Button>
                            }

                        </div>
                    </>
                    :
                    // bike returned
                    null
                }

                <div className={'button-list'}>
                    {bikePickedUp || bikeReturned ?
                        null
                    :
                        <Button className={'button'} onClick={handleCancelBookingClick}>{t('cancel_booking')}</Button>
                    }
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Booking;