import '../../Style.css';
import React, {startTransition, Suspense, useState} from 'react';
import {Button} from 'antd';
import logo_img from '../../logo.png';
import Loading from '../Loading';
import {useTranslation} from 'react-i18next';
import LanguageMenu from '../LanguageMenu';
import {useLocation, useNavigate} from "react-router-dom";
import {URI_ADMIN_BAN} from "../../constants/URIs";
import {URL_MENU} from "../../constants/URLs";
import {ERR_POSTING_BAN, ERR_POSTING_ENROLLMENT} from "../../constants/messages/ErrorMessages";

/**
 * Handles the page for user ban by admin.
 *
 * @returns {Element}
 * @constructor
 */
const UserBan = () => {
    const { t } = useTranslation('translations/admin/UserBan');
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [email, setEmail] = useState('');

    const handleEmailInput = (value) => {
        setEmail(value);
    }

    // POST user ban request
    const postBan = () => {
        let payload = {
            contact_data: email
        };
        fetch(URI_ADMIN_BAN, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })

            .then(response => {
                if (response) {
                    alert('User banned successfully.');
                    navigateToNextPage();
                }
                else {
                    return response.json().then((errorText) => {
                        throw new Error(errorText.detail);
                    });
                }
            })
            .catch(error => {
                alert(ERR_POSTING_BAN + ' ' + error.message);
            })
    }

    const handleBanClick = () => {
        postBan();
    }

    const navigateToNextPage = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>

                    <h3>{t('email')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => handleEmailInput(e.target.value)}
                    />
                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleBanClick}>{t('ban_user')}</Button>
                    </div>
                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default UserBan;