import {useLocation, useNavigate} from "react-router-dom";
import {Button} from "antd";
import '../../Style.css';
import logo_img from "../../logo.png";
import React, {startTransition, Suspense, useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import LanguageMenu from "../LanguageMenu";
import Loading from "../Loading";
import {
    URI_BOOKING_BIKE_BOOKING,
    URI_BOOKING_BIKE_STORE,
    URI_BOOKING_BIKE,
    URI_IDX_1,
} from "../../constants/URIs";
import {URL_BOOKING_FILTER, URL_BOOKING_BIKE, URL_IDX_1} from "../../constants/URLs";
import {ERR_POSTING_BOOKING} from "../../constants/messages/ErrorMessages";

/**
 * Handles the confirmation dialog at the booking process.
 *
 * @returns {Element}
 * @constructor
 */
const BookingCheckout = () => {
    const { t } = useTranslation('translations/booking/BookingCheckout');
    const navigate = useNavigate();

    const [bike, setBike] = useState('');
    const [store, setStore] = useState('');

    const state = useLocation().state;
    const token = state?.token;
    const bookingData = state?.booking_data;

    // GET bike
    const fetchBike = () => {
        fetch(URI_BOOKING_BIKE.replace(URI_IDX_1, bookingData.bike))
            .then(response => {
                return response.json()
            })
            .then(data => {
                setBike(data)
            })
    }

    // GET store
    const fetchStore = () => {
        fetch(URI_BOOKING_BIKE_STORE.replace(URI_IDX_1, bookingData.bike))
            .then(response => {
                return response.json()
            })
            .then(data => {
                setStore(data)
            })
    }

    useEffect(() => {
        fetchBike()
        fetchStore()
    }, [])

    // POST booking
    const postBooking = () => {
        let timeZoneOffset = (new Date()).getTimezoneOffset() * 60000;

        let startDate = new Date(new Date(bookingData.startDate).getTime() - timeZoneOffset);
        let endDate = new Date(new Date(bookingData.endDate).getTime() - timeZoneOffset);

        let payload = {
            begin: startDate.toISOString().split('T')[0],
            end: endDate.toISOString().split('T')[0],
            equipment: bookingData.equipment
        };
        return fetch(URI_BOOKING_BIKE_BOOKING.replace(URI_IDX_1, bookingData.bike), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })
    }

    const handleConfirmClick = () => {
        postBooking()
            .then(response => {
                if (response.ok) {
                    alert(t('booking_successful'));
                    startTransition(() => {
                        navigate(URL_BOOKING_FILTER, {
                            state: {
                                token: token
                            }
                        });
                    });
                } else {
                    // Handle unsuccessful account deletion
                    throw new Error(ERR_POSTING_BOOKING)
                }
            })
            .catch(error => {
                alert(error);
            });
    }

    const handleCancelClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_BIKE.replace(':', '').replace(URL_IDX_1, bookingData.bike), {
                state: {
                    token: token
                }
            });
        });
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_BIKE.replace(':', '').replace(URL_IDX_1, bookingData.bike), {
                state: {
                    startDate: bookingData.startDate,
                    endDate: bookingData.endDate,
                    token: token
                }
            })
        })
    }

    return (

        <Suspense fallback={<Loading />}>
            <div className={"view-container"}>
                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('heading')}</h1>
                <div className={"content-container"}>
                    <h2>{t('wanna_confirm')}</h2>
                    <p>{t('bike')}: {bike.name}</p>
                    <p>{t('store')}: {store.name}</p>
                    { bookingData.equipment.length !== 0 ?
                        <p>{t('equipment')}: {bookingData.equipment?.join(', ')}</p>
                        :
                        <p>{t('equipment')}: {t('none')}</p>
                    }
                    <p>{t('from')}: {bookingData.startDate.toLocaleDateString('de-DE')}</p>
                    <p>{t('to')}: {bookingData.endDate.toLocaleDateString('de-DE')}</p>
                </div>
                <div className={"button-list"}>
                    <Button className={"accent-button"} onClick={handleConfirmClick}>{t('confirm_booking')}</Button>
                    <Button className={"button"} onClick={handleCancelClick}>{t('cancel')}</Button>
                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default BookingCheckout;