import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import i18n from './i18n';

import {
    URL_ADMIN_BOOKING_MANAGEMENT,
    URL_ADMIN_BOOKING,
    URL_ADMIN_ENROLLMENT,
    URL_ADMIN_USER_BAN,
    URL_ADMIN_BIKE_MANAGEMENT,
    URL_ADMIN_BIKE,
    URL_ADMIN_BIKE_REGISTRATION,
    URL_ADMIN_STORE_MANAGEMENT,
    URL_ADMIN_STORE,
    URL_ADMIN_STORE_REGISTRATION,
    URL_BOOKING_BIKE,
    URL_BOOKING_CHECKOUT,
    URL_BOOKING_STORE,
    URL_BOOKING_FILTER,
    URL_MANAGER_BIKE_MANAGEMENT,
    URL_MANAGER_BIKE,
    URL_MANAGER_BIKE_REGISTRATION,
    URL_MANAGER_BOOKING_MANAGEMENT,
    URL_MANAGER_BOOKING,
    URL_MANAGER_STORE,
    URL_MANAGER_ENROLLMENT,
    URL_USER_ACCOUNT_MANAGEMENT,
    URL_USER_BOOKING_MANAGEMENT,
    URL_USER_BOOKING,
    URL_USER_LOGIN,
    URL_MENU,
    URL_USER_REGISTER,
    URL_USER_EMAIL_VERIFICATION
} from "./constants/URLs";

import AdminBookingManagement from './components/admin/BookingManagement';
import AdminBooking from './components/admin/Booking';
import AdminEnrollment from './components/admin/Enrollment';
import AdminUserBan from './components/admin/UserBan';
import AdminBikeManagement from './components/admin/BikeManagement.js';
import AdminBike from './components/admin/Bike.js';
import AdminBikeRegistration from './components/admin/BikeRegistration.js';
import AdminStoreManagement from './components/admin/StoreManagement.js';
import AdminStore from './components/admin/Store.js';
import AdminStoreRegistration from './components/admin/StoreRegistration.js';

import BookingBike from './components/booking/Bike';
import BookingBookingCheckout from './components/booking/BookingCheckout';
import BookingFilter from './components/booking/Filter';
import BookingStore from './components/booking/Store';

import ManagerConfiguration from './components/manager/Store';
import ManagerManagerEnrollment from './components/manager/Enrollment';
import ManagerBikeManagement from './components/manager/BikeManagement';
import ManagerBike from './components/manager/Bike';
import ManagerBikeRegistration from './components/manager/BikeRegistration';
import ManagerBookingManagement from './components/manager/BookingManagement';
import ManagerBooking from './components/manager/Booking';

import UserMenu from './components/user/Menu';
import UserLogin from './components/user/Login';
import UserRegister from './components/user/Register';
import UserAccountManager from './components/user/AccountManagement';
import UserBookingManagement from './components/user/BookingManagement';
import UserBooking from './components/user/Booking';
import UserEmailVerification from './components/user/EmailVerification';

import Home from "./components/user/Home";

function App() {
    return (
        <BrowserRouter>
            <Routes>

                <Route path='/' element={<Home />} />

                <Route path={URL_ADMIN_BOOKING_MANAGEMENT} element={<AdminBookingManagement />} />
                <Route path={URL_ADMIN_BOOKING} element={<AdminBooking />} />
                <Route path={URL_ADMIN_ENROLLMENT} element={<AdminEnrollment />} />
                <Route path={URL_ADMIN_USER_BAN} element={<AdminUserBan />} />
                <Route path={URL_ADMIN_BIKE_MANAGEMENT} element={<AdminBikeManagement />} />
                <Route path={URL_ADMIN_BIKE} element={<AdminBike />} />
                <Route path={URL_ADMIN_BIKE_REGISTRATION} element={<AdminBikeRegistration />} />
                <Route path={URL_ADMIN_STORE_MANAGEMENT} element={<AdminStoreManagement />} />
                <Route path={URL_ADMIN_STORE} element={<AdminStore />} />
                <Route path={URL_ADMIN_STORE_REGISTRATION} element={<AdminStoreRegistration />} />

                <Route path={URL_BOOKING_FILTER} element={<BookingFilter />} />
                <Route path={URL_BOOKING_BIKE} element={<BookingBike />} />
                <Route path={URL_BOOKING_CHECKOUT} element={<BookingBookingCheckout />} />
                <Route path={URL_BOOKING_STORE} element={<BookingStore />} />

                <Route path={URL_MANAGER_STORE} element={<ManagerConfiguration />} />
                <Route path={URL_MANAGER_ENROLLMENT} element={<ManagerManagerEnrollment />} />
                <Route path={URL_MANAGER_BIKE_MANAGEMENT} element={<ManagerBikeManagement/>} />
                <Route path={URL_MANAGER_BIKE} element={<ManagerBike />} />
                <Route path={URL_MANAGER_BIKE_REGISTRATION} element={<ManagerBikeRegistration />} />
                <Route path={URL_MANAGER_BOOKING_MANAGEMENT} element={<ManagerBookingManagement />} />
                <Route path={URL_MANAGER_BOOKING} element={<ManagerBooking />} />

                <Route path={URL_MENU} element={<UserMenu />} />
                <Route path={URL_USER_LOGIN} element={<UserLogin />} />
                <Route path={URL_USER_REGISTER} element={<UserRegister />} />
                <Route path={URL_USER_ACCOUNT_MANAGEMENT} element={<UserAccountManager />} />
                <Route path={URL_USER_BOOKING_MANAGEMENT} element={<UserBookingManagement />} />
                <Route path={URL_USER_BOOKING} element={<UserBooking />} />
                <Route path={URL_USER_EMAIL_VERIFICATION} element={<UserEmailVerification />} />

            </Routes>
        </BrowserRouter>
    );
}

export default App;
