import React, {startTransition, Suspense, useEffect, useState} from 'react';
import {useLocation, useParams} from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import logo_img from '../../logo.png';
import { Button } from 'antd';
import 'react-calendar/dist/Calendar.css';
import '../../Style.css';
import { useTranslation } from 'react-i18next';
import LanguageMenu from '../LanguageMenu';
import Loading from '../Loading';
import {
    URI_BOOKING_BIKE, URI_BOOKING_BIKE_STORE, URI_HOST,
    URI_IDX_1
} from "../../constants/URIs";
import {
    URL_BOOKING_CHECKOUT, URL_BOOKING_FILTER,
    URL_BOOKING_STORE,
    URL_IDX_1,
    URL_USER_LOGIN,
    URL_USER_REGISTER
} from "../../constants/URLs";
import Select from "react-select";
import {ERR_FETCHING_BIKE, ERR_FETCHING_STORE} from "../../constants/messages/ErrorMessages";

/**
 * Handles the bike page during the booking process.
 *
 * @returns {Element}
 * @constructor
 */
const Bike = () => {
    const { t } = useTranslation('translations/booking/Bike');
    const navigate = useNavigate();
    const params = useParams();

    const state = useLocation().state;
    const token = state?.token;

    const startDate = state?.startDate;
    const endDate =  state?.endDate;

    const [bike, setBike] = useState('');
    const [store, setStore] = useState('');

    const [openLoginSignInDialog, setOpenLoginSignInDialog] = useState(false);

    const [allEquipmentOptions, setAllEquipmentOptions] = useState([])
    const [selectedEquipment, setSelectedEquipment] = useState([]);


    // GET bike
    const fetchBike = () => {
        fetch(URI_BOOKING_BIKE.replace(URI_IDX_1, params.idx1))
            .then(response => response.json())
            .then(data => {
                setBike(data);
                setAllEquipmentOptions(data.equipment.map((equipment) => (
                    {value: equipment.equipment, label: equipment.equipment}
                )));
                fetchStore(data.store)
            })
            .catch(error => {
                console.error(ERR_FETCHING_BIKE, error);
            });
    };

    // GET store
    const fetchStore = () => {
        fetch(URI_BOOKING_BIKE_STORE.replace(URI_IDX_1, params.idx1))
            .then(response => response.json())
            .then(data => {
                setStore(data);
            })
            .catch(error => {
                console.error(ERR_FETCHING_STORE, error);
            });
    }
    useEffect(() => {
        fetchBike();
    }, []);

    const handleEquipmentChange = (options) => {
        setSelectedEquipment(options.map(option => option.value));
    }

    const handleBookClick = () => {
        if (!token) {
            setOpenLoginSignInDialog(true);
        } else {
            startTransition(() => {
                navigate(URL_BOOKING_CHECKOUT.replace(':', '').replace(URL_IDX_1, params.idx1), {
                    state: {
                        booking_data: {
                            bike: params.idx1,
                            store: store.name,
                            equipment: selectedEquipment,
                            startDate: startDate,
                            endDate: endDate
                        },
                        token: token
                    },
                });
            })
        }
    };

    const handleStoreClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_STORE.replace(':', '').replace(URL_IDX_1, params.idx1), {
                state: {token: token}
            });
        })
    };

    const handleLoginClick = () => {
        startTransition(() => {
            navigate(URL_USER_LOGIN, {
                state: {
                    booking_data: {
                        bike: params.idx1,
                        store: store.name,
                        equipment: selectedEquipment,
                        startDate: startDate,
                        endDate: endDate
                    }
                }
            })
        })
    }

    const handleSignInClick = () => {
        startTransition(() => {
            navigate(URL_USER_REGISTER, {
                state: {
                    booking_data: {
                        bike: params.idx1,
                        store: store.name,
                        equipment: selectedEquipment,
                        startDate: startDate,
                        endDate: endDate
                    }
                }
            })
        })
    }

    const handleCancelClick = () => {
        setOpenLoginSignInDialog(false);
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_FILTER, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>
                    {t('heading')}: {bike.name}
                </h1>

                { openLoginSignInDialog ?
                        <div className={'content-container'}>
                            <h3>{t('login_to_rent')}</h3>
                            <div className={'button-list'}>
                                <Button className={'accent-button'} onClick={handleLoginClick}>{t('login')}</Button>
                                <Button className={'button'} onClick={handleSignInClick}>{t('sign_in')}</Button>
                                <Button className={'button'} onClick={handleCancelClick}>{t('cancel')}</Button>
                            </div>
                        </div>
                    :
                        <>
                            <div className={'content-container'}>
                                <h2>{bike.name}</h2>
                                <img className={'img'} src={URI_HOST + bike.image} alt="BikeImage" width={'100%'} />
                                <p>{bike.description}</p>
                            </div>
                            <div className={'content-container'}>
                                <h3>{t('select_equipment')}</h3>
                                <Select
                                    isMulti
                                    placeholder={"None"}
                                    options={allEquipmentOptions}
                                    name='regions'
                                    onChange={handleEquipmentChange}
                                    className='basic-multi-select'
                                    classNamePrefix='select'
                                />
                            </div>
                            <div className={'content-container'}>
                                <h2>{t('dates_of_renting')} </h2>

                                {/*<div className='calendar-container'>*/}
                                {/*    <Calendar*/}
                                {/*        onChange={handleCalendarChange}*/}
                                {/*        selectRange={true}*/}
                                {/*        value={selectedDateMargins} />*/}
                                {/*</div>*/}

                                <p>{t('from')}: {startDate?.toLocaleDateString('de-DE')}</p>
                                <p>{t('to')}: {endDate?.toLocaleDateString('de-DE')}</p>

                                <div className={'button-list'}>
                                    <Button className={'accent-button'} onClick={handleBookClick}>{t('book')}</Button>
                                </div>
                            </div>
                            <div className={'clickable-content-container'} onClick={handleStoreClick}>
                                <h2>{store.name}</h2>
                                <p>{t('address')}: {store.address}</p>
                            </div>
                        </>
                }

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
};

export default Bike;
