
import '../../Style.css';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate} from 'react-router-dom';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {URI_ADMIN_BIKES, URI_HOST} from '../../constants/URIs';
import {
    URL_ADMIN_BIKE,
    URL_IDX_1,
    URL_MENU
} from '../../constants/URLs';
import {Button} from 'antd';


/**
 * Handles the overview page of the bikes in the system from the admin perspective.
 *
 * @returns {Element}
 * @constructor
 */
const BikeManagement = () => {
    const { t } = useTranslation('translations/admin/BikeManagement');
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [bikes, setBikes] = useState([]);

    useEffect(() => {
        const fetchBikes = async () => {
            const response = await fetch(
                URI_ADMIN_BIKES,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Token ${token}`,
                    }
                }
            );
            const data = await response.json();
            setBikes(data);
        };
        fetchBikes();
    }, [])

    const handleBikeClick = (bikeID) => {
        startTransition(() => {
            navigate(URL_ADMIN_BIKE.replace(':', '').replace(URL_IDX_1, bikeID), {state: {token: token}});
        });
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    const BikeList = () => {
        if (bikes.length === 0) {
            return <p>{t('no_bikes')}</p>;
        } else {
            return (
                <>
                    {bikes.map((bike) => (
                        <div
                            key={bike.id}
                            className='clickable-content-container'
                            onClick={() => handleBikeClick(bike.id)}
                        >
                            <h2>{bike.name}</h2>
                            <img className={'img'} src={URI_HOST + bike.image} alt={'BikeImage'}/>
                        </div>
                    ))}
                </>
            );
        }
    };

    return (
        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                <BikeList />

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default BikeManagement;