
import '../../Style.css';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import Loading from '../Loading';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate} from "react-router-dom";
import LanguageMenu from "../LanguageMenu";
import {URI_ADMIN_BOOKINGS} from "../../constants/URIs";
import {URL_ADMIN_BOOKING, URL_IDX_1, URL_MENU} from "../../constants/URLs";
import {Button} from "antd";

/**
 * Handles the overview page of the bookings in the system from the admin perspective.
 *
 * @returns {Element}
 * @constructor
 */
const BookingManagement = () => {
    const { t } = useTranslation('translations/admin/BookingManagement');
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [bookings, setBookings] = useState([]);

    const fetchBookings = async () => {
        const response = await fetch(URI_ADMIN_BOOKINGS, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`,
            }
        });
        const data = await response.json();
        setBookings(data);
    };

    useEffect(() => {
        fetchBookings();
    }, [])

    const handleBookingClick = (id) => {
        startTransition(() => {
            navigate(URL_ADMIN_BOOKING.replace(':', '').replace(URL_IDX_1, id), {state: {token: token}});
        });
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}});
        });
    }

    const BookingList = () => {
        if (bookings.length === 0) {
            return <p>{t('no_bookings')}</p>;
        } else {
            return (
                <>
                    {bookings.map((booking) => (
                        <div
                            key={booking.id}
                            className='clickable-content-container'
                            onClick={() => handleBookingClick(booking.id)}
                        >
                            <p>{t('user')}: {booking.preferred_username}</p>
                            <p>{t('start_date')}: {booking.begin}</p>
                            <p>{t('end_date')}: {booking.end}</p>
                            <p>{t('equipment')}: {booking.equipment?.map(e => e.equipment).join(', ')}</p>
                            <p>{t('booking_status')}: {booking.booking_status.map(e => e.booking_status).join(', ')}</p>
                        </div>
                    ))}
                </>
            );
        }
    };

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <BookingList />

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default BookingManagement;