
import '../../Style.css';
import React from "react";
import Login from "./Login";

/**
 * Handles the configurable home page.
 *
 * @returns {Element}
 * @constructor
 */
const Home = () => {

    const HomePageContent = () => {
        return <Login />;
    }


    return (
        <HomePageContent />
    );
}

export default Home;