import '../../Style.css';
import React, {startTransition, Suspense, useEffect, useState} from "react";
import logo_img from "../../logo.png";
import {useTranslation} from "react-i18next";
import Loading from "../Loading";
import LanguageMenu from "../LanguageMenu";
import {useLocation, useNavigate} from "react-router-dom";
import {URI_USER_BOOKINGS} from "../../constants/URIs";
import {URL_IDX_1, URL_MENU, URL_USER_BOOKING} from "../../constants/URLs";
import {Button} from "antd";

/**
 * Handles the overview page of all bookings by a user.
 *
 * @returns {Element}
 * @constructor
 */
const BookingManagement = () => {
    const navigate = useNavigate();
    const { t } = useTranslation('translations/user/BookingManagement');

    const token = useLocation().state.token;

    const [bookings, setBookings] = useState([]);

    // GET bookings
    useEffect(() => {
        const fetchBookings = async () => {
            const response = await fetch(URI_USER_BOOKINGS, {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Token ${token}`,
                }
            });
            const data = await response.json();
            setBookings(data);
        };
        fetchBookings();
    }, [])


    const handleBookingClick = (bookingString) => {
        startTransition(() => {
            navigate(URL_USER_BOOKING.replace(':', '').replace(URL_IDX_1, bookingString), {state: {token: token}})
        });
    }

    const BookingList = () => {
        if (bookings.length === 0) {
            return <p>{t('no_bookings')}</p>;
        } else {
            return (
                <>
                    {bookings.map((booking) => (
                        <div
                            key={booking.id}
                            className='clickable-content-container'
                            onClick={() => handleBookingClick(booking.id)}
                        >
                            <p>{t('start_date')}: {booking.begin}</p>
                            <p>{t('end_date')}: {booking.end}</p>
                            <p>{t('booking_status')}: {booking.booking_status.map(e => e.booking_status).join(', ')}</p>
                        </div>
                    ))}
                </>
            );
        }
    };

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}});
        });
    }

    return (

        <Suspense fallback={<Loading />}>

            <div className={"view-container"}>
                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('heading')}</h1>
                <BookingList />

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default BookingManagement;