
import '../../Style.css';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate} from 'react-router-dom';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {URI_HOST, URI_MANAGER_BIKES, URI_MANAGER_STORE} from '../../constants/URIs';
import {
    URL_MANAGER_BIKE,
    URL_IDX_1,
    URL_MENU, URL_MANAGER_BIKE_REGISTRATION
} from '../../constants/URLs';
import {Button} from 'antd';
import {ERR_FETCHING_STORE} from "../../constants/messages/ErrorMessages";

/**
 * Handles the overview page of all bikes of a particular store.
 *
 * @returns {Element}
 * @constructor
 */
const BikeManagement = () => {
    const { t } = useTranslation('translations/manager/BikeManagement');
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [bikes, setBikes] = useState([]);

    const [storeId, setStoreId] = useState('');

    // GET bikes
    useEffect(() => {
        const fetchBikes = async () => {
            const response = await fetch(
                URI_MANAGER_BIKES,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Token ${token}`,
                    }
                }
            );
            const data = await response.json();
            setBikes(data);
        };
        fetchBikes();
    }, [])

    const fetchStore = () => {
        fetch(URI_MANAGER_STORE, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`,
            }
        })
            .then(response =>  response.json())
            .then(data => {
                setStoreId(data);
            })
            .catch(error => {
                console.error(ERR_FETCHING_STORE, error);
            });
    }

    useEffect(() => {
        fetchStore();
    }, []);

    const handleBikeClick = (bikeID) => {
        startTransition(() => {
            navigate(URL_MANAGER_BIKE.replace(':', '').replace(URL_IDX_1, bikeID), {state: {token: token}});
        });
    }

    const handleAddBikeClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BIKE_REGISTRATION.replace(':', '').replace(URL_IDX_1, storeId), {
                state: {token: token}
            });
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    const BikeList = () => {
        if (bikes.length === 0) {
            return <p>{t('no_bikes')}</p>;
        } else {
            return (
                <>
                    {bikes.map((bike) => (
                        <div
                            key={bike.id}
                            className='clickable-content-container'
                            onClick={() => handleBikeClick(bike.id)}
                        >
                            <h2>{bike.name}</h2>
                            <img className={'img'} src={URI_HOST + bike.image} alt={'BikeImage'}/>
                        </div>
                    ))}
                </>
            );
        }
    };

    return (
        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                <BikeList />

                <div className={'button-list'}>
                    <Button className={'accent-button'} onClick={handleAddBikeClick}>{t('add_bike')}</Button>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default BikeManagement;