
import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import Loading from '../Loading';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate, useParams} from "react-router-dom";
import LanguageMenu from "../LanguageMenu";
import {
    URI_IDX_1, URI_USER_BOOKING, URI_USER_BOOKED_BIKE, URI_USER_BOOKED_STORE, URI_USER_BOOKING_CANCEL, URI_HOST
} from "../../constants/URIs";
import {
    URL_BOOKING_BIKE, URL_BOOKING_STORE,
    URL_IDX_1, URL_USER_BOOKING_MANAGEMENT
} from "../../constants/URLs";
import {ERR_FETCHING_BIKE, ERR_FETCHING_BOOKING, ERR_FETCHING_STORE} from "../../constants/messages/ErrorMessages";

/**
 * Handles the booking info page from customer perspective.
 *
 * @returns {Element}
 * @constructor
 */
const Booking = () => {
    const { t } = useTranslation('translations/user/Booking');
    const params = useParams();
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [booking, setBooking] = useState('');
    const [bike, setBike] = useState('');
    const [store, setStore] = useState('');

    const [statusList, setStatusList] = useState([]);

    // GET booking
    const fetchBooking = () => {
        fetch(URI_USER_BOOKING.replace(URI_IDX_1, params.idx1), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                setBooking(data);
                setStatusList(data.booking_status.map(e => e.booking_status))
                fetchBike(params.idx1);
                fetchStore(params.idx1);
            })
            .catch(error => { console.error(ERR_FETCHING_BOOKING, error); });
    }

    // GET bike
    const fetchBike = (bookingId) => {
        fetch(URI_USER_BOOKED_BIKE.replace(URI_IDX_1, bookingId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setBike(data) })
            .catch(error => { console.error(ERR_FETCHING_BIKE, error); });
    };

    // GET store
    const fetchStore = (bookingId) => {
        fetch(URI_USER_BOOKED_STORE.replace(URI_IDX_1, bookingId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setStore(data) })
            .catch(error => { console.error(ERR_FETCHING_STORE, error); })
    }

    useEffect(() => {
        fetchBooking();
    }, []);

    // POST booking cancellation
    const cancelBooking = () => {
        fetch(URI_USER_BOOKING_CANCEL.replace(URI_IDX_1, params.idx1), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    // If the request was not successful, throw an error
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    const handleBikeClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_BIKE.replace(':', '').replace(URL_IDX_1, bike.id), {state: {token: token}});
        })
    }

    const handleStoreClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_STORE.replace(':', '').replace(URL_IDX_1, store.id), {state: {token: token}});
        })
    }

    const handleCancelBookingClick = () => {
        cancelBooking();
        startTransition(() => {
            navigate(URL_USER_BOOKING_MANAGEMENT, {state: {token: token}})
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_USER_BOOKING_MANAGEMENT, {state: {token: token}});
        });
    }

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>
                    <h2>{t('booked_dates')}</h2>
                    <p>{t('from')}: {booking.begin}</p>
                    <p>{t('to')}: {booking.end}</p>
                    { booking.equipment?.length !== 0 ?
                        <p>{t('equipment')}: {booking.equipment?.map(e => e.equipment).join(', ')}</p>
                        :
                        <p>{t('equipment')}: {t('none')}</p>
                    }
                    <p>{t('status')}: {statusList.join(', ')}</p>
                    <p></p>
                </div>
                <div className={'clickable-content-container'} onClick={handleBikeClick}>
                    <h2>{bike.name}</h2>
                    <img className={'img'} src={URI_HOST + bike.image} alt="BikeImage" width={'100%'}></img>
                </div>
                <div className={'clickable-content-container'} onClick={handleStoreClick}>
                    <h2>{store.name}</h2>
                    <p>{store.address}</p>
                </div>

                { statusList.includes('Cancelled') ?
                    null
                    :
                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleCancelBookingClick}>{t('cancel_booking')}</Button>
                    </div>
                }

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Booking;