import React, {startTransition, Suspense, useEffect, useState} from 'react';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import '../../Style.css';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_BOOKING_BIKE_STORE, URI_BOOKING_REGIONS,
    URI_IDX_1
} from '../../constants/URIs';
import {URL_BOOKING_BIKE, URL_IDX_1} from '../../constants/URLs';
import {Button} from "antd";
import {ERR_FETCHING_REGIONS, ERR_FETCHING_STORE} from "../../constants/messages/ErrorMessages";

/**
 * Handles the store info page during the booking proceess.
 *
 * @returns {Element}
 * @constructor
 */
const Store = () => {
    const { t } = useTranslation('translations/booking/Store');
    const navigate = useNavigate();
    const params = useParams();

    const state = useLocation().state;
    const token = state?.token;

    const [store, setStore] = useState('');

    const [allRegions, setAllRegions] = useState([]);

    // GET all regions
    const fetchAllRegions = () => {
        fetch(URI_BOOKING_REGIONS, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(response => {
                return response.json()
            })
            .then(data => {
                setAllRegions(data)
            })
            .catch(error => {
                console.error(ERR_FETCHING_REGIONS, error);
            });
    }

    const allRegionObjects = allRegions.map((region) => (
        {value: region[0], name: region[1]}
    ));

    // GET store
    const fetchStore = () => {
        fetch(URI_BOOKING_BIKE_STORE.replace(URI_IDX_1, params.idx1))
            .then(response => response.json())
            .then(data => {
                setStore(data);
            })
            .catch(error => {
                console.error(ERR_FETCHING_STORE, error);
            });
    }
    useEffect(() => {
        fetchStore();
        fetchAllRegions();
    }, []);

    const getRegionNameByValue = (value) => {
        return allRegionObjects.find(obj => obj.value === value)?.name;
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_BIKE.replace(':', '').replace(URL_IDX_1, params.idx1), {
                state: {token: token}
            });
        });
    };

    return (
        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{store.name}</h1>
                <div className={'content-container'}>
                    <h2>{t('contact_data')}</h2>
                    <p>{t('address')}: {store.address}</p>
                    <p>{t('region')}: {getRegionNameByValue(store.region)}</p>
                    <p>{t('email')}: {store.email}</p>
                    <p>{t('phone')}: {store.phone_number}</p>

                    <h2>{t('opening_hours')}</h2>

                    { !store.mon_opened ?
                        <p>{t('mon')}: {t('closed')}</p>
                        :
                        <p>{t('mon')}: {store.mon_open} - {store.mon_close}</p>
                    }

                    { !store.tue_opened ?
                        <p>{t('tue')}: {t('closed')}</p>
                        :
                        <p>{t('tue')}: {store.tue_open} - {store.tue_close}</p>
                    }

                    { !store.wed_opened ?
                        <p>{t('wed')}: {t('closed')}</p>
                        :
                        <p>{t('wed')}: {store.wed_open} - {store.wed_close}</p>
                    }

                    { !store.thu_opened ?
                        <p>{t('thu')}: {t('closed')}</p>
                        :
                        <p>{t('thu')}: {store.thu_open} - {store.thu_close}</p>
                    }

                    { !store.fri_opened ?
                        <p>{t('fri')}: {t('closed')}</p>
                        :
                        <p>{t('fri')}: {store.fri_open} - {store.fri_close}</p>
                    }

                    { !store.sat_opened ?
                        <p>{t('sat')}: {t('closed')}</p>
                        :
                        <p>{t('sat')}: {store.sat_open} - {store.sat_close}</p>
                    }

                    { !store.sun_opened ?
                        <p>{t('sun')}: {t('closed')}</p>
                        :
                        <p>{t('sun')}: {store.sun_open} - {store.sun_close}</p>
                    }


                    <h2>{t('prep_time')}</h2>
                    <p>{store.prep_time}</p>

                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Store;