
import '../../Style.css';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate} from 'react-router-dom';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {URI_ADMIN_STORES} from '../../constants/URIs';
import {
    URL_ADMIN_STORE_REGISTRATION, URL_ADMIN_STORE,
    URL_IDX_1,
    URL_MENU
} from '../../constants/URLs';
import {Button} from 'antd';

/**
 * Handles the overview page of all stores in the system.
 *
 * @returns {Element}
 * @constructor
 */
const StoreManagement = () => {
    const { t } = useTranslation('translations/admin/StoreManagement');
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [stores, setStores] = useState([]);


    // GET stores
    useEffect(() => {
        const fetchStores = async () => {
            const response = await fetch(
                URI_ADMIN_STORES,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Token ${token}`,
                    }
                }
            );
            const data = await response.json();
            setStores(data);
        };
        fetchStores();
    }, []);

    const handleStoreClick = (storeID) => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX_1, storeID), {state: {token: token}});
        });
    }

    const handleAddStoreClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE_REGISTRATION, {state: {token: token}});
        });
    }

    const StoreList = () => {
        if (stores.length === 0) {
            return <p>{t('no_stores')}</p>;
        }

        return (
            <>
                {stores.map((store) => (
                    <div
                        key={store.id}
                        className='clickable-content-container'
                        onClick={() => handleStoreClick(store.id)}
                    >
                        <h2>{store.name}</h2>
                        <p>{store.address}</p>
                    </div>
                ))}
            </>
        );
    };

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                <StoreList />

                <div className={'button-list'}>
                    <Button className={'accent-button'} onClick={handleAddStoreClick}>{t('add_store')}</Button>
                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default StoreManagement;