import {Button} from 'antd';
import '../../Style.css';
import logo_img from '../../logo.png';
import React, {startTransition, Suspense, useEffect, useState} from 'react';

import { useTranslation } from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from "../LanguageMenu";
import {useLocation, useNavigate} from "react-router-dom";
import {URI_USER_DATA, URI_USER_LOGOUT} from "../../constants/URIs";
import {
    URL_USER_ACCOUNT_MANAGEMENT,
    URL_USER_BOOKING_MANAGEMENT,
    URL_BOOKING_FILTER,
    URL_MANAGER_STORE,
    URL_MANAGER_ENROLLMENT,
    URL_MANAGER_BIKE_MANAGEMENT,
    URL_MANAGER_BOOKING_MANAGEMENT,
    URL_MANAGER_HANDOUT,
    URL_ADMIN_BOOKING_MANAGEMENT,
    URL_ADMIN_BIKE_MANAGEMENT,
    URL_ADMIN_STORE_MANAGEMENT, URL_ADMIN_USER_BAN, URL_ADMIN_ENROLLMENT, URL_HOME
} from "../../constants/URLs";
import {ERR_FETCHING_USER} from "../../constants/messages/ErrorMessages";

/**
 * Handles the menu page from admin, manager and/or customer perspective.
 *
 * @returns {Element}
 * @constructor
 */
const Menu = () => {
    const { t } = useTranslation('translations/user/Menu');
    const navigate = useNavigate();

    let token = useLocation().state?.token;

    if (token === undefined) {

        let currentURL = window.location.search;

        let urlParams = new URLSearchParams(currentURL);
        token = urlParams.get("token");
        console.log('t', token)
    }

    const [isAdmin, setIsAdmin] = useState(false);
    const [isManager, setIsManager] = useState(false);

    // GET user
    const fetchUserData = () => {
        fetch(URI_USER_DATA, {
            headers: {
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {

                let statusList = data.user_status.map((e) => e.user_status);

                setIsAdmin(checkIfAdmin(statusList))
                setIsManager(checkIfManager(statusList))
            })
            .catch(error => { console.error(ERR_FETCHING_USER, error);});
    };
    useEffect(() => {
        fetchUserData();
    }, []);

    const checkIfAdmin = (statusList) => {
        return statusList.includes('Administrator')
    }

    const checkIfManager = (statusList) => {
        return statusList.some(item => item.startsWith('Store'));
    }

    const handleAdminBookingManagementClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_BOOKING_MANAGEMENT, {state: {token: token}});
        });
    }

    const handleAdminBikeManagementClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_BIKE_MANAGEMENT, {state: {token: token}});
        });
    }

    const handleAdminStoreManagementClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE_MANAGEMENT, {state: {token: token}});
        });
    }

    const handleAdminEnrollmentClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_ENROLLMENT, {state: {token: token}});
        });
    }

    const handleUserBanClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_USER_BAN, {state: {token: token}});
        })
    }

    const handleManagerStoreConfigurationClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_STORE, {state: {token: token}});
        })
    }

    const handleManagerEnrolmentClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_ENROLLMENT, {state: {token: token}});
        })
    }

    const handleManagerBikeManagementClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BIKE_MANAGEMENT, {state: {token: token}});
        })
    }

    const handleManagerBookingManagementClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BOOKING_MANAGEMENT, {state: {token: token}});
        })
    }

    const handleRentingClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_FILTER, {state : {token: token}});
        })
    }

    const handleBookingManagementClick = () => {
        startTransition(() => {
            navigate(URL_USER_BOOKING_MANAGEMENT, {state : {token: token}});
        });
    }

    const handleAccountManagementClick = () => {
        startTransition(() => {
            navigate(URL_USER_ACCOUNT_MANAGEMENT, {state : {token: token}});
        });
    }

    const handleLogoutClick = () => {
        logout();
    }

    function logout() {

        // Send the POST request to the server endpoint
        fetch(URI_USER_LOGOUT, {
            method: 'POST',
            headers: {
                'Authorization': `Token ${token}`
            }
        })
        .then(response => {
            if (response.ok) {
                startTransition(() => {
                    navigate(URL_HOME);
                });
            } else {
                // If the request was not successful throw an error
                throw new Error(response.detail);
            }
        })
        .catch(error => {
            alert('Error making the logout request.');
        });
    }

    return (
        <Suspense fallback={<Loading />}>
        <div className={'view-container'}>
            <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
            <h1>{t('heading')}</h1>

            { isAdmin ?
                <>
                    <h2>{t('admin_menu')}</h2>
                    <div className={'menu-button-list'}>
                        <Button className={'button'} onClick={handleAdminBookingManagementClick}>{t('booking_management')}</Button>
                        <Button className={'button'} onClick={handleAdminBikeManagementClick}>{t('bike_management')}</Button>
                        <Button className={'button'} onClick={handleAdminStoreManagementClick}>{t('store_management')}</Button>
                        <Button className={'button'} onClick={handleAdminEnrollmentClick}>{t('enrollment')}</Button>
                        <Button className={'button'} onClick={handleUserBanClick}>{t('user_ban')}</Button>
                    </div>
                </>
                :
                null
            }

            { isManager ?
                <>
                    <h2>{t('manager_menu')}</h2>
                    <div className={'menu-button-list'}>
                        <Button className={'button'} onClick={handleManagerStoreConfigurationClick}>{t('store_configuration')}</Button>
                        <Button className={'button'} onClick={handleManagerEnrolmentClick}>{t('manager_enrollment')}</Button>
                        <Button className={'button'} onClick={handleManagerBikeManagementClick}>{t('bike_management')}</Button>
                        <Button className={'button'} onClick={handleManagerBookingManagementClick}>{t('booking_management')}</Button>
                    </div>
                </>
                :
                null
            }

            <h2>{t('user_menu')}</h2>
            <div className={'menu-button-list'}>
                <Button className={'accent-button'} onClick={handleRentingClick}>{t('bike_renting')}</Button>
                <Button className={'button'} onClick={handleBookingManagementClick}>{t('booking_management')}</Button>
                <Button className={'button'} onClick={handleAccountManagementClick}>{t('account_management')}</Button>
                <Button className={'button'} onClick={handleLogoutClick}>{t('logout')}</Button>
            </div>

            <LanguageMenu />
        </div>
        </Suspense>
    );
}

export default Menu;